import { PageDirection, SortDir, SortField } from '@api/types';
import { PayloadAction } from '@reduxjs/toolkit';

export interface StateWithLoadingAndError {
    loading: boolean;
    error:
    | string
    | null
    | {
        message?: string;
        data?: {
            errors: { [key: string]: string[] };
        };
    };
}

export function startLoading(state: StateWithLoadingAndError) {
    state.loading = true;
}

export function stopLoading(state: StateWithLoadingAndError) {
    state.loading = false;
}

export function loadingFailed(
    state: StateWithLoadingAndError,
    action: PayloadAction<string>
) {
    state.loading = false;
    state.error = action.payload;
}

export const pageInfoInit = {
    hasNextPage: false,
    hasPreviousPage: false,
    pageSize: 10,
    pageDirection: PageDirection.After,
    cursor: '',
};
export const sortInit = {
    dir: SortDir.Asc,
    field: SortField.Title,
};
