import React from 'react';
import { Button, Spinner } from '@shopify/polaris';
import './progress.scss';
import { startPollUninstallStatus } from '@redux/reducers/uninstall';
import { useDispatch } from 'react-redux';

function Progress({
    text = 'Preparation for application removal is in progress...',
}: {
    text?: string;
}) {
    const dispatch = useDispatch();
    React.useEffect(() => {
        dispatch(startPollUninstallStatus());
    }, []);

    return (
        <div className="appUninstall__preparation">
            <div className="appUninstall__step__content">{text}</div>
            <Spinner />
        </div>
    );
}

export default Progress;
