import { ActionsObservable, ofType, StateObservable } from 'redux-observable';
import { EMPTY, of } from 'rxjs';
import { catchError, filter, map, switchMap } from 'rxjs/operators';
import subscriptionApi from '@api/subscription';
import { apiHost } from '@api/axios';
import {
    getSubscription,
    getSubscriptionSuccess,
    getSubscriptionFailure,
    getActiveSubscriptionSuccess,
    getActiveSubscriptionFailure,
    getActiveSubscription,
    switchToPlan,
} from '@redux/reducers/subscription';
import { withAppLoading, withConfirmation } from './utils';
import { RootState } from '@redux/reducers/rootReducer';
import { ActionFrom } from '@api/types';
import { Redirect } from '@shopify/app-bridge/actions';
import { startAppLoading } from '@redux/reducers/app';

export const getSubscriptionEpic = (actions$: ActionsObservable<any>) =>
    actions$.pipe(
        ofType<any>(getSubscription),
        withAppLoading(() => {
            return subscriptionApi.getSubscription().pipe(
                map((subscription) => getSubscriptionSuccess(subscription)),
                catchError((err) => of(getSubscriptionFailure(err)))
            );
        })
    );
export const getActiveSubscriptionEpic = (actions$: ActionsObservable<any>) =>
    actions$.pipe(
        ofType<any>(getActiveSubscription),
        withAppLoading(() => {
            return subscriptionApi.getActiveSubscription().pipe(
                map((subscription) =>
                    getActiveSubscriptionSuccess(subscription)
                ),
                catchError((err) => of(getActiveSubscriptionFailure(err)))
            );
        })
    );

export const switchToPlanEpic = (
    actions$: ActionsObservable<any>,
    state$: StateObservable<RootState>
) =>
    actions$.pipe(
        ofType(switchToPlan),
        switchMap((action: ActionFrom<typeof switchToPlan>) => {
            const {
                payload: { app, id, withConfirmation: withConfirmationFlag },
            } = action;
            const {
                auth: { headers, originalHeaders },
            } = state$.value;

            // const searchParams = Object.entries(headers).filter(
            //     (entry) => entry[1] !== null
            // ) as [string, string][];

            const callback = () => {
                // const redirect = Redirect.create(app);
                // const params = new URLSearchParams({
                //     resource_name: 'Product',
                //     edit: editFields.join(',')
                //   })
                // redirect.dispatch(Redirect.Action.REMOTE as any, {
                //     path: apiHost + `/billing/${id}`,
                // });
                // app.dispatch(
                //     Redirect.toRemote({
                //         url: apiHost + `/billing/${id}`,
                //     })
                // );
                window.location.assign(
                    apiHost +
                    `/billing/${id}?${new URLSearchParams(
                        originalHeaders
                    ).toString()}`
                );

                return of(startAppLoading());
            };

            if (!withConfirmationFlag) {
                return callback();
            }

            return of(action).pipe(
                withConfirmation<typeof switchToPlan>(
                    (action) => ({
                        content: `By switching to the Free Plan, all products that are currently enabled with PreOrder Boss will be disabled. Are you sure you want to proceed? `,
                    }),
                    callback
                )
            );
        })
    );
