import React, { useCallback, useMemo } from 'react';
import {
    DisplayText,
    Icon,
    Modal,
    Stack,
    TextContainer,
} from '@shopify/polaris';
import { useFormat } from '@contexts/LocaleContext';
import messages from './messages';

import { useDispatch, useSelector } from 'react-redux';
import { confirmationSelector } from '@redux/selectors/app';
import {
    handlePrimaryConfirmationAction,
    handleSecondaryConfirmationAction,
} from '@redux/reducers/app';
import { RiskMajor } from '@shopify/polaris-icons';
import './confirmation.scss';

function Confirmation() {
    const { formatDictionary } = useFormat();
    const dispatch = useDispatch();
    const { content, open, actions, warning } = useSelector(
        confirmationSelector
    );
    const handlePrimary = useCallback(
        () => dispatch(handlePrimaryConfirmationAction()),
        []
    );
    const handleSecondary = useCallback(
        () => dispatch(handleSecondaryConfirmationAction()),
        []
    );

    const primaryAction = useMemo(
        () =>
            actions === false
                ? undefined
                : {
                      content: actions?.primary ?? 'Yes',
                      onAction: handlePrimary,
                  },
        [actions]
    );

    const secondaryActions = useMemo(
        () =>
            actions === false
                ? undefined
                : [
                      {
                          content: actions?.secondary ?? 'No',
                          onAction: handleSecondary,
                      },
                  ],
        [actions]
    );

    return (
        <Modal
            open={open}
            title=""
            onClose={handleSecondary}
            primaryAction={primaryAction}
            secondaryActions={secondaryActions}
        >
            <Modal.Section>
                <div className="confirmationModal">
                    {!warning ? null : (
                        <Icon source={RiskMajor} color="yellow" />
                    )}
                    <DisplayText size="medium">{content}</DisplayText>
                </div>
            </Modal.Section>
        </Modal>
    );
}
export default Confirmation;
