/* eslint-disable @typescript-eslint/no-unsafe-assignment */
import { intl } from '@contexts/LocaleContext';
import { toggleConfirmation, toggleToast } from '@redux/reducers/app';
import { AxiosError, AxiosRequestConfig } from 'axios';
import Axios from 'axios-observable';
import store from '../redux/store';
import { buildFormData, jsonToFormData } from './utils.ts/buildFormData';

const axios = Axios;

let apiHost =
    __preorder_env__?.REACT_APP_API_HOST || process.env.REACT_APP_API_HOST!;
if (!apiHost) {
    throw new Error(
        'REACT_APP_API_HOST environment variable should be provided'
    );
}

if (apiHost.endsWith('/')) {
    apiHost = apiHost.slice(0, -1);
}

const baseURL = apiHost + '/api';

const instance = axios.create({
    baseURL,
});

instance.interceptors.request.use(function (config) {
    const headersFromState = store.getState().auth.headers;
    const headers = Object.keys(headersFromState).reduce((acc, headerName) => {
        const headerValue = headersFromState[headerName];
        if (headerValue) {
            acc[headerName] = headerValue;
        }
        return acc;
    }, {});
    // eslint-disable-next-line @typescript-eslint/no-unsafe-assignment
    // eslint-disable-next-line @typescript-eslint/no-unsafe-member-access
    config.headers.common = { ...config.headers.common, ...headers };

    const withFormData: AxiosRequestConfig['method'][] = [
        'POST',
        'post',
        'PUT',
        'put',
        'PATCH',
        'patch',
    ];

    if (withFormData.includes(config.method)) {
        config.data = jsonToFormData(config.data);
        // eslint-disable-next-line
        config.headers['Content-Type'] = 'multipart/form-data';
    }

    return config;
});

instance.interceptors.response.use(
    (response) => response,
    (err: AxiosError<any>) => {
        if (err.isAxiosError) {
            const { message, code, response } = err;
            const data = response?.data;
            const status = response?.status;

            if ([423].includes(status ?? 200)) {
                store.dispatch(
                    toggleConfirmation({
                        // eslint-disable-next-line @typescript-eslint/no-unsafe-member-access
                        content: data?.message,
                        actions: false,
                        warning: true,
                    })
                );
                throw { message, code, data, status };
            }

            store.dispatch(toggleToast(errorServerToastPayload));
            throw { message, code, data, status };
        }
        return err;
    }
);

function getToastPayload(): Parameters<typeof toggleToast>[0] {
    const content = intl.formatMessage({
        id: 'app.serverError',
        defaultMessage: 'Server Error',
    });

    return { content, error: true };
}

const errorServerToastPayload = getToastPayload();

export { apiHost };
export default instance;
