import { ActionsObservable, ofType, StateObservable } from 'redux-observable';
import { of } from 'rxjs';
import { catchError, map, switchMap } from 'rxjs/operators';
import settingsApi from '@api/settings';
import {
    getSettings,
    getSettingsSucces,
    getSettingsFailure,
    updateSettings,
    updateSettingsSuccess,
    updateSettingsFailure,
} from '@redux/reducers/settings';
import { mapWithToast, withAppLoading } from './utils';
import { RootState } from '@redux/reducers/rootReducer';
import { ActionFrom, Nullable } from '@api/types';
import messages from './messages';

export const getSettingsEpic = (actions$: ActionsObservable<any>) =>
    actions$.pipe(
        ofType<any>(getSettings),
        withAppLoading(() => {
            return settingsApi.getSettings().pipe(
                map((settings) => getSettingsSucces(settings)),
                catchError((err) => of(getSettingsFailure(err)))
            );
        })
    );

export const updateSettingsEpic = (
    actions$: ActionsObservable<any>,
    state$: StateObservable<RootState>
) =>
    actions$.pipe(
        ofType(updateSettings),
        withAppLoading((action: ActionFrom<typeof updateSettings>, state) => {
            type Params = Parameters<typeof settingsApi.updateSettings>[0];
            const params: Params = {
                app_settings: state.settings.settings.app,
                design_settings: {
                    current:
                        state.settings.settings.design.state?.current ?? null,
                },
            };

            return settingsApi.updateSettings(params).pipe(
                mapWithToast(messages.setSettingsSuccess, () =>
                    updateSettingsSuccess(true)
                ),
                catchError((err) => of(updateSettingsFailure(err)))
            );
        })
    );

