import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { UninstallStatus } from '../../api/types/uninstall';
import { UninstalStatusResponse } from '../../api/uninstall';
import { StateWithLoadingAndError } from './common/reducers';

interface UninstallState extends StateWithLoadingAndError {
    status: UninstallStatus | null;
    polling: boolean;
}

const initialState: UninstallState = {
    loading: false,
    error: null,
    status: null,
    polling: false,
};

const uninstallSlice = createSlice({
    name: 'uninstall',
    initialState,
    reducers: {
        startPollUninstallStatus(state) {
            state.polling = true;
        },
        stopPollUninstallStatus(state) {
            state.polling = false;
        },
        getUninstallStatus(state) {
            state.loading = false;
        },
        getUninstallStatusSuccess(
            state,
            { payload }: PayloadAction<UninstalStatusResponse>
        ) {
            state.loading = false;
            state.status = payload.prepare_to_uninstall_status;
            state.polling =
                payload.prepare_to_uninstall_status ===
                UninstallStatus.Progress;
        },
        getUninstallStatusFailure(state, { payload }: PayloadAction<unknown>) {
            state.loading = false;
            state.error = (payload as string) || null;
            state.polling = false;
        },
        startUninstallPreparation(state) {
            state.loading = false;
        },
        startUninstallPreparationSuccess(state) {
            state.loading = false;
            state.status = UninstallStatus.Progress;
        },
        startUninstallPreparationFailure(
            state,
            { payload }: PayloadAction<unknown>
        ) {
            state.loading = false;
            state.error = (payload as null) || null;
            state.polling = false;
        },
    },
    extraReducers: () => {
        // builder.addCase(openSettingsModal, (state, action) => {
        //     state.profileModal = initialState.profileModal;
        //, });
    },
});

export const {
    startPollUninstallStatus,
    stopPollUninstallStatus,
    getUninstallStatus,
    getUninstallStatusSuccess,
    getUninstallStatusFailure,
    startUninstallPreparation,
    startUninstallPreparationSuccess,
    startUninstallPreparationFailure,
} = uninstallSlice.actions;

export default uninstallSlice.reducer;
