import React, { useCallback } from 'react';
import { Toast } from '@shopify/polaris';

import { useDispatch, useSelector } from 'react-redux';
import { toastSelector } from '@redux/selectors/app';
import './app-toast.scss';
import { toggleToast } from '@redux/reducers/app';

function AppToast() {
    const toast = useSelector(toastSelector);
    const dispatch = useDispatch();
    const onDismiss = useCallback(() => dispatch(toggleToast()), []);

    if (toast === null) {
        return null;
    }

    const { content, duration, error } = toast;

    return toast ? (
        <Toast
            content={content}
            duration={duration}
            error={error}
            onDismiss={onDismiss}
        />
    ) : null;
}
export default AppToast;
