import {
    AxiosClientError,
    Filter,
    FiltersState,
    FilterValue,
    PageInfo,
    ProductSettingsDTO,
    Profile,
    ProfileDTO,
    ProfileInState,
    SortState,
} from '@api/types';
import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { ProfilesResponse } from '../../api/profiles';
import {
    loadingFailed,
    pageInfoInit,
    sortInit,
    startLoading,
    stopLoading,
    StateWithLoadingAndError,
} from './common/reducers';
import { getSettingsFailure, getSettingsSucces } from './settings';
// import { openSettingsModal } from './products';

type FetchProfilesParams = undefined | { filterValue?: FilterValue };

interface ProfilesState extends StateWithLoadingAndError {
    // profiles: Profile[] | null;
    profilesById: Record<string, ProfileInState>;
    profilesIds: string[];
    filters: FiltersState;
    pageInfo: PageInfo;
    loading: boolean;
    error: string | null;
    selectedProfiles: string[];
    sort: SortState;
    // filters: FiltersState;
    profileModal: {
        loading: boolean;
        open: boolean;
        data: Partial<ProfileDTO> | null;
    } & StateWithLoadingAndError;
}

const initialState: ProfilesState = {
    profilesById: {},
    profilesIds: [],
    loading: false,
    error: null,
    pageInfo: pageInfoInit,
    filters: {
        query: '',
        status: '',
        availiability: '',
    },
    selectedProfiles: [],
    sort: sortInit,
    profileModal: {
        loading: false,
        data: null,
        error: null,
        open: false,
    },
};

// export const fetchProfiles = (): AppThunk => async (dispatch) => {
//     try {
//         dispatch(getProfilesStart());
//         const profiles = await profilesApi.getProfiles().toPromise();
//         dispatch(getProfilesSuccess(profiles.profiles));
//     } catch (err) {
//         dispatch(getProfilesFailure(err));
//     }
// };

const fetchProfilesReducer = (
    state: ProfilesState,
    { payload: params }: PayloadAction<FetchProfilesParams>
) => {
    if (params) {
        const { filterValue } = params;
        if (filterValue) {
            state.filters[filterValue.filter] = filterValue.value as '';
        }

        state.pageInfo = initialState.pageInfo;
    } else {
        startLoading(state);
    }
};

const profilesSlice = createSlice({
    name: 'profiles',
    initialState,
    reducers: {
        startLoading,
        stopLoading,
        getProfilesStart: startLoading,
        getDefaultSettingsForProfile(state) {
            state.profileModal.loading = true;
        },
        getProfilesSuccess(
            state,
            { payload }: PayloadAction<ProfilesResponse>
        ) {
            // const { profiles, pageInfo } = payload;
            const profiles = payload;
            // state.pageInfo = { ...state.pageInfo, ...pageInfo };
            state.loading = false;
            state.error = null;

            const byId = profiles.reduce(
                (acc, profile) => {
                    acc.profiles[profile.id] = profile;
                    acc.profilesIds.push(profile.id);

                    return acc;
                },
                { profiles: {}, profilesIds: [] } as {
                    profiles: ProfilesState['profilesById'];
                    profilesIds: string[];
                }
            );

            state.profilesById = byId.profiles;
            state.profilesIds = byId.profilesIds;
        },
        getProfilesFailure: loadingFailed,

        // Profiles Selection
        selectProfiles(
            state,
            {
                payload: profilesIds,
            }: PayloadAction<typeof initialState.selectedProfiles>
        ) {
            state.selectedProfiles = profilesIds;
        },
        //epics
        fetchProfiles: fetchProfilesReducer,
        fetchProfilesWithQuery: fetchProfilesReducer,
        fetchProfilesWithQueryStart: startLoading,
        openProfileModal(state, { payload: open }: PayloadAction<boolean>) {
            // if (!open) {
            state.profileModal.data = initialState.profileModal.data;
            // } else {
            state.profileModal.open = open;
            // }
        },
        updateProfile(state, { payload }: PayloadAction<ProfileDTO>) {
            state.profileModal.loading = true;
        },
        updateProfileSuccess(state, { payload }: PayloadAction<boolean>) {
            state.profileModal.loading = false;
            state.profileModal.open = false;
        },
        updateProfileFailure(state, { payload }: PayloadAction<string>) {
            state.profileModal.loading = false;
            state.profileModal.error = payload;
        },
        fetchProfile(state: ProfilesState, { payload }: PayloadAction<string>) {
            state.profileModal.loading = true;
        },
        fetchProfileSuccess(state, { payload }: PayloadAction<ProfileDTO>) {
            state.profileModal.loading = false;
            state.profileModal.data = payload;
            state.profileModal.error = null;
        },
        fetchProfileFailure(state, action: PayloadAction<string>) {
            state.profileModal.loading = false;
            state.profileModal.error = action.payload;
        },
        deleteProfile(
            state: ProfilesState,
            { payload }: PayloadAction<Profile>
        ) {
            // state.profileModal.loading = true;
        },
        deleteProfileSuccess(state, { payload }: PayloadAction<boolean>) {
            state.loading = false;
        },
        deleteProfileFailure(state, action: PayloadAction<AxiosClientError>) {
            state.loading = false;
        },
    },
    extraReducers: (builder) => {
        // builder.addCase(openSettingsModal, (state, action) => {
        //     state.profileModal = initialState.profileModal;
        // });
        builder
            .addCase(getSettingsSucces, (state, action) => {
                state.profileModal.loading = false;
            })
            .addCase(getSettingsFailure, (state, action) => {
                state.profileModal.open = false;
                state.profileModal.loading = false;
            });
    },
});

export const {
    getProfilesStart,
    getProfilesSuccess,
    getProfilesFailure,
    fetchProfiles,
    fetchProfilesWithQuery,
    fetchProfilesWithQueryStart,
    getDefaultSettingsForProfile,
    openProfileModal,
    updateProfile,
    updateProfileSuccess,
    updateProfileFailure,
    selectProfiles,
    fetchProfile,
    fetchProfileSuccess,
    fetchProfileFailure,
    deleteProfile,
    deleteProfileSuccess,
    deleteProfileFailure,
    startLoading: startLoadingProfiles,
    stopLoading: stopLoadingProfiles,
} = profilesSlice.actions;

export default profilesSlice.reducer;
