import React from 'react';
import { AppProvider } from '@shopify/polaris';
import { BrowserRouter } from 'react-router-dom';
import { ConnectedRouter } from 'connected-react-router';
import { Provider, ReactReduxContext } from 'react-redux';
import store, { history } from './redux/store';
import AppContainer from '@components/layout/AppContainer';
import Link from '@components/utils/Link';
import { LocaleProvider, LocaleContext } from '@contexts/LocaleContext';
import { IntlProvider } from 'react-intl';
import en from '@shopify/polaris/locales/en.json';
import './stylesheets/style.scss';
import AppInstall from './AppInstall';
import AppBridgeProvider from './app-bridge/AppBridgeProvider';
import RoutePropagator from './app-bridge/RoutePropagator';

const i18n = en;
function App() {
    return (
        <Provider store={store}>
            <ConnectedRouter history={history}>
                <LocaleProvider>
                    <LocaleContext.Consumer>
                        {({ config: locale }) => (
                            <IntlProvider {...locale}>
                                <AppProvider i18n={i18n} linkComponent={Link}>
                                    {/* <AppBridgeProvider> */}
                                    <AppContainer />
                                    {/* </AppBridgeProvider> */}
                                </AppProvider>
                            </IntlProvider>
                        )}
                    </LocaleContext.Consumer>
                </LocaleProvider>
            </ConnectedRouter>
        </Provider>
    );
}

export default App;
