import { Observable, of } from 'rxjs';
import { delay, map } from 'rxjs/operators';
import { DeepNullable } from 'ts-essentials';
import axios from './axios';
import isMocksMode from './mocks/mock-env';
import subscriptionList from './mocks/subscription/subscriptionList';
import { AppSettingsDTO, AppSettingsInState } from './types/settings';
import { SubscriptionDTO } from './types/subscription';

const prefix = '/plans';

export type SubscriptionResponse = SubscriptionDTO[];

const getSubscription = () => {
    const url = `${prefix}`;

    if (isMocksMode()) {
        return of(subscriptionList).pipe(delay(800));
    }

    return axios.get<SubscriptionResponse>(url).pipe(map((res) => res.data));
};
const getActiveSubscription = () => {
    const url = `${prefix}/active`;

    if (isMocksMode()) {
        return of(subscriptionList[1]).pipe(delay(800));
    }

    return axios.get<SubscriptionDTO>(url).pipe(map((res) => res.data));
};

const updateSubscription = (
    settings: AppSettingsInState
): Observable<boolean> => {
    const url = `${prefix}`;

    return axios.post<boolean>(url, settings).pipe(map((res) => res.data));
};

const settingsApi = {
    getSubscription,
    getActiveSubscription,
    updateSubscription,
} as const;

export default settingsApi;
