import { Observable, of } from 'rxjs';
import { delay, map } from 'rxjs/operators';
import axios from './axios';
import isMocksMode from './mocks/mock-env';
import appSettings from './mocks/app/appSettings';
import {
    AppSettingsDTO,
    AppSettingsInState,
    AppSettingsParam,
} from './types/settings';

const prefix = '/settings';

const getSettings = () => {
    const url = `${prefix}`;

    if (isMocksMode()) {
        return of(appSettings).pipe(delay(800));
    }

    return axios.get<AppSettingsDTO>(url).pipe(map((res) => res.data));
};

const updateSettings = (settings: AppSettingsParam): Observable<boolean> => {
    const url = `${prefix}`;

    if (isMocksMode()) {
        return of(true).pipe(delay(800));
    }

    return axios.post<boolean>(url, settings).pipe(map((res) => res.data));
};

const settingsApi = {
    getSettings,
    updateSettings,
} as const;

export default settingsApi;
