import { RootState } from '@redux/reducers/rootReducer';
import { createSelector } from '@reduxjs/toolkit';

const selectAppState = (state: RootState) => state.app;

export const confirmationSelector = createSelector(
    selectAppState,
    (sub) => sub.confirmation
);

export const toastSelector = createSelector(selectAppState, (sub) => sub.toast);

export const mockSelector = createSelector(selectAppState, (sub) => sub.mock);

export const isLoadingAppSelector = createSelector(
    selectAppState,
    (sub) => sub.loading
);

export const statusSelector = createSelector(
    selectAppState,
    (sub) => sub.status
);

// export const moneyFormatSelector = createSelector(selectAppState, (sub) => ({
//     money_format: sub.money_format,
//     currency_shopify_format: sub.currency_shopify_format,
// }));


const money_formatSelector = createSelector(
    selectAppState,
    (sub) => sub.money_format
);

const currency_shopify_formatSelector = createSelector(
    selectAppState,
    (sub) => sub.currency_shopify_format
);

export const moneyFormatSelector = createSelector(
    money_formatSelector,
    currency_shopify_formatSelector,
    (money_format, currency_shopify_format) => ({
        money_format,
        currency_shopify_format,
    }));

export const currencySymbolSelector = createSelector(
    selectAppState,
    (sub) => sub.currency_symbol
);

export const timezoneOffsetSelector = createSelector(
    selectAppState,
    (sub) => sub.timezone_offset
);

export const errorSelector = createSelector(selectAppState, (sub) => sub.error);
export const openedPopoverSelector = createSelector(
    selectAppState,
    (sub) => sub.openedPopover
);
