const productsSlice = createSlice({
    name: PREFIX,
    initialState,
    reducers: {
        startLoading,
        //  Products Fetching
        getProductsStart: startLoading,
        getProductsSuccess(
            state,
            { payload }: PayloadAction<ProductsResponse>
        ) {
            const { products, pageInfo } = payload;
            // state.products = products;
            state.pageInfo = { ...state.pageInfo, ...pageInfo };
            state.loading = false;
            state.error = null;

            const byId = products.reduce(
                (acc, product) => {
                    const { variants, ...productRest } = product;
                    // let different = false;
                    let allDisabled = true;
                    let allActivated = true;

                    const variantsInState: ProductsState['variantsById'] = {};
                    Object.values(variants).forEach((variant) => {
                        const variantEnabled = getPreorderEnableStatus(variant);

                        if (!isDefaultVariant(variant)) {
                            //     different =
                            //         different || variantEnabled !== productEnabled;
                            allDisabled = allDisabled && !variantEnabled;
                            allActivated =
                                allActivated &&
                                variant.preorder_status ===
                                PreorderStatus.Activated;
                        } else {
                            allDisabled = false;
                            allActivated = false;
                            product.preorder_status = variant.preorder_status;
                        }
                        variantsInState[variant.id] = {
                            ...variant,
                            enabled: variantEnabled,
                        };
                    });

                    if (allDisabled) {
                        product.preorder_status = PreorderStatus.Disabled;
                    }
                    if (allActivated) {
                        product.preorder_status = PreorderStatus.Activated;
                    }

                    const productEnabled = getPreorderEnableStatus(product);
                    acc.products[product.id] = {
                        ...productRest,
                        preorder_status: product.preorder_status,
                        variants: Object.keys(variants),
                        // different,
                        enabled: productEnabled,
                    };
                    acc.productsIds.add(product.id);
                    acc.variants = { ...acc.variants, ...variantsInState };

                    return acc;
                },
                { products: {}, productsIds: new Set(), variants: {} } as {
                    products: ProductsState['productsById'];
                    productsIds: Set<string>;
                    variants: ProductsState['variantsById'];
                }
            );

            state.productsById = byId.products;
            state.productsIds = Array.from(byId.productsIds);
            state.variantsById = byId.variants;
        },
        getProductsFailure: loadingFailed,

        // Product Settings fetching
        getProductSettingsSuccess(
            state,
            { payload }: PayloadAction<ProductSettingsResponse>
        ) {
            // const { products, pageInfo } = payload;
            // // state.products = products;
            // state.pageInfo = { ...state.pageInfo, ...pageInfo };
            state.settingsModal.loading = false;
            state.settingsModal.data = payload;
            state.error = null;
        },
        getProductSettingsFailure(state, { payload }: PayloadAction<string>) {
            state.settingsModal = {
                ...initialState.settingsModal,
                error: payload,
            };
            // state.settingsModal.error = payload;
        },

        // Products Selection
        selectProducts(
            state,
            {
                payload: { productsIds },
            }: PayloadAction<Omit<typeof initialState.selectedProducts, 'all'>>
        ) {
            const selectedProductsIds = productsIds.filter(
                (id) => state.productsById[id] !== undefined
            );

            state.selectedProducts.productsIds = selectedProductsIds;

            state.selectedProducts.all =
                selectedProductsIds.length === state.productsIds.length;

            state.selectedProducts.variantsIds =
                selectedProductsIds.reduce((acc, id) => {
                    acc.push(...state.productsById[id].variants);
                    return acc;
                }, [] as string[]) ?? [];
        },
        selectVariants(
            state,
            {
                payload: { selected, productId },
            }: PayloadAction<{ selected: string[]; productId: string }>
        ) {
            // const selectedSet = new Set([
            //     ...state.selectedProducts.variantsIds,
            //     ...selected,
            // ]);
            const stateVariantsIds = [...state.selectedProducts.variantsIds];

            const productVariants = state.productsById[productId].variants;

            productVariants.forEach((s) =>
                removeItemFromArray(stateVariantsIds, s)
            );
            stateVariantsIds.push(...selected);

            if (productVariants.length === selected.length) {
                if (!state.selectedProducts.productsIds.includes(productId)) {
                    state.selectedProducts.productsIds.push(productId);
                }
            } else {
                removeItemFromArray(
                    state.selectedProducts.productsIds,
                    productId
                );
            }

            state.selectedProducts.variantsIds = [...stateVariantsIds];
            state.selectedProducts.all =
                state.selectedProducts.productsIds.length ===
                state.pageInfo.pageSize;
        },

        // Paging
        setPage(state, { payload }: PayloadAction<PageDirection>) {
            if (payload !== PageDirection.Same) {
                state.pageInfo.pageDirection = payload;
            }
            const pageInfo = state.pageInfo;

            if (payload === PageDirection.After && state.pageInfo.hasNextPage) {
                const idOfLastProduct =
                    state.productsIds[state.productsIds.length - 1];
                pageInfo.cursor = state.productsById[idOfLastProduct].cursor;
            } else if (
                payload === PageDirection.Before &&
                state.pageInfo.hasPreviousPage
            ) {
                const idOfFirstProduct = state.productsIds[0];
                pageInfo.cursor = state.productsById[idOfFirstProduct].cursor;
            }

            state.selectedProducts = initialState.selectedProducts;

            if (payload == PageDirection.Same) {
                startLoading(state);
            } else {
                startLoadingAndCollapse(state);
            }
        },

        // Filters
        setSort(state, { payload }: PayloadAction<SortState>) {
            state.sort = payload;
            state.pageInfo = initialState.pageInfo;

            state.selectedProducts = initialState.selectedProducts;
            startLoadingAndCollapse(state);
        },

        // Filters
        setFilter(
            state,
            { payload: { filter, value } }: PayloadAction<FilterValue>
        ) {
            state.filters[filter] = value as '';
        },
        setFilterQuery(state, { payload }: PayloadAction<string>) {
            state.filters.query = payload;
        },
        setFilterStatus(state, { payload }: PayloadAction<FilterStatus>) {
            state.filters.status = payload;
        },
        setFilterAvailiability(
            state,
            { payload }: PayloadAction<FilterAvailiability>
        ) {
            state.filters.availiability = payload;
        },

        // Folding
        setUnfolded(
            state,
            { payload }: PayloadAction<{ id: string; unfolded: boolean }>
        ) {
            if (payload.unfolded) {
                state.unfoldedProductsIds.push(payload.id);
            } else {
                removeItemFromArray(state.unfoldedProductsIds, payload.id);
            }
        },

        // Enable/Disable
        setStatus(
            state,
            {
                payload,
            }: PayloadAction<{
                id: string;
                enabled: boolean;
                isVariant: boolean | undefined;
            }>
        ) { },
        bulkEnable(state, { payload }: PayloadAction<false | undefined>) { },
        bulkEnableSuccess(state, { payload }: PayloadAction<boolean>) {
            state.loading = false;
        },
        bulkEnableFailureByPlan(state) {
            state.loading = false;
        },
        bulkEnableFailure(state, action: PayloadAction<string>) {
            state.loading = false;
        },
        openAssignProfileModal(
            state,
            {
                payload,
            }: PayloadAction<
                typeof initialState.assignProfileModal.data | undefined
            >
        ) {
            state.assignProfileModal.open = true;
            state.assignProfileModal.data = payload;
        },
        closeAssignProfileModal(state, action: PayloadAction<undefined>) {
            state.assignProfileModal = initialState.assignProfileModal;
        },
        assignProfile(
            state,
            { payload }: PayloadAction<{ id: string; item?: Variant }>
        ) {
            state.assignProfileModal.loading = false;
            state.assignProfileModal.open = false;
            state.settingsModal.open = false;
            state.loading = true;
        },
        assignProfileSuccess(state, { payload }: PayloadAction<boolean>) {
            state.assignProfileModal = initialState.assignProfileModal;
        },
        assignProfileFailure(state, { payload }: PayloadAction<string>) {
            state.assignProfileModal.loading = false;
            state.assignProfileModal.error = payload;
            state.assignProfileModal.open = true;
            state.loading = false;
        },
        unassignProfile(
            state,
            {
                payload,
            }: PayloadAction<{ id?: string; item?: Variant } | undefined>
        ) {
            // state.assignProfileModal.loading = true;
            // state.loading = true;
        },
        startUnassignProfiles(state, { payload }: PayloadAction<undefined>) {
            // state.assignProfileModal.loading = true;
            state.loading = true;
        },
        unassignProfileSuccess(state, { payload }: PayloadAction<boolean>) {
            state.assignProfileModal = initialState.assignProfileModal;
            state.loading = false;
        },
        unassignProfileFailure(state, { payload }: PayloadAction<string>) {
            // state.assignProfileModal.loading = false;
            // state.assignProfileModal.error = payload;
            state.loading = false;
        },
        setSettings(state, { payload }: PayloadAction<UpdateSettingsParams>) {
            state.settingsModal.loading = true;
        },
        setSettingsSuccess(state, { payload }: PayloadAction<boolean>) {
            state.settingsModal = initialState.settingsModal;
        },
        setSettingsFailure(state, { payload }: PayloadAction<string>) {
            state.settingsModal.loading = false;
            state.settingsModal.error = payload;
        },
        openSettingsModal(state, { payload: open }: PayloadAction<boolean>) {
            state.settingsModal.open = open;
            if (!open) {
                state.settingsModal = initialState.settingsModal;
            }
        },
        updateProductsAndVariantsStatusBulk(
            state,
            {
                payload,
            }: PayloadAction<{
                enabled: boolean;
                products: ProductInState[];
                variants: VariantInState[];
            }>
        ) {
            const { products, variants, enabled } = payload;
            startLoading(state);

            products.forEach((item) => {
                const product = state.productsById[item.id];
                product.enabled = enabled;
            });
            variants.forEach((item) => {
                const variant = state.variantsById[item.id];
                variant.enabled = enabled;
            });
        },
        //epics
        fetchProducts: fetchProductsReducer,
        fetchProductsWithQuery: fetchProductsReducer,
        fetchProductsWithQueryStart: startLoadingAndCollapse,
        fetchProductSettings: fetchProductSettingsReducer,
    },
    extraReducers: (builder) => {
        // builder.addCase(
        //     fetchProducts.fulfilled,
        //     (state, action: PayloadAction<typeof initialState.products>) => {
        //         state.products = action.payload;
        //     }
        // );

        builder
            .addCase(openProfileModal, (state, action) => {
                state.settingsModal.open = true;
            })
            .addCase(updateProfileSuccess, (state, action) => {
                state.settingsModal.open = false;
            })
            .addCase(getSettingsFailure, (state, action) => {
                // state.settingsModal.open = false;
            });
    },
});

export const {
    getProductsStart,
    getProductsSuccess,
    getProductsFailure,
    selectProducts,
    setFilter,
    fetchProducts,
    fetchProductsWithQueryStart,
    fetchProductsWithQuery,
    setPage,
    setSort,
    setUnfolded,
    selectVariants,
    setStatus,
    setSettings,
    openSettingsModal,
    updateProductsAndVariantsStatusBulk,
    fetchProductSettings,
    getProductSettingsSuccess,
    getProductSettingsFailure,
    setSettingsSuccess,
    setSettingsFailure,
    bulkEnable,
    bulkEnableSuccess,
    bulkEnableFailureByPlan,
    bulkEnableFailure,
    openAssignProfileModal,
    closeAssignProfileModal,
    assignProfile,
    assignProfileSuccess,
    assignProfileFailure,
    unassignProfile,
    unassignProfileSuccess,
    unassignProfileFailure,
    startUnassignProfiles,

    startLoading: startLoadingProducts,
} = productsSlice.actions;

export default productsSlice.reducer;
