const en = {
    locale: 'en',
    messages: {},
};

/**
 * Substitues property name instead of value for each property in object
 * @param obj
 */
type Messages = typeof en.messages;
function injectnameofs(obj: Messages) {
    type KeyType = keyof Messages;
    const result = {} as { [key in KeyType]: key };
    for (const prop in obj) {
        (function (p, o) {
            Object.defineProperty(result, p, {
                get: function () {
                    return p;
                },
            });
        })(prop, obj);
    }

    return result;
}

const strings = injectnameofs(en.messages);

export { strings, en };
