import React, { useEffect } from 'react';
import { Switch, Route, Redirect } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { Page, DisplayText, Layout, Frame } from '@shopify/polaris';
import TopNavBar from './TopNavbar/TopNavbar';
import { fetchProducts } from '@redux/reducers/products';
import { fetchProfiles } from '@redux/reducers/profiles';
import asyncComponent from '@components/HOC/asyncComponent';
import Confirmation from './Confirmation/Confirmation';
import ToggleMock from './ToggleMock';
import GlobalLoadingOverlay from './GlobalLoadingOverlay';
import AppToast from './AppToast/AppToast';
import { errorSelector, statusSelector } from '@redux/selectors/app';
import { UninstallStatus } from '@api/types/uninstall';
import { push } from 'connected-react-router';
import { startPollAppState } from '@redux/reducers/app';
import { AppStatus } from '@api/types/app';
import Progress from '@components/Uninstall/Steps/Progress/Progress';
import { NoAvailableApp } from './NoAvailableApp/NoAvailableApp';

const ProductsList = asyncComponent(
    () => import('@components/Products/ProductsList/ProductsList')
);

const Uninstall = asyncComponent(
    () => import('@components/Uninstall/Uninstall')
);

const ProfilesList = asyncComponent(
    () =>
        import(
            /* webpackPrefetch: true */ '@components/Profiles/ProfilesList/ProfilesList'
        )
);

const AppSettings = asyncComponent(
    () =>
        import(
            /* webpackPrefetch: true */ '@components/AppSettings/AppSettings'
        )
);

const Subscription = asyncComponent(
    () =>
        import(
            /* webpackPrefetch: true */ '@components/Subscription/Subscription'
        )
);

const Box = ({ children, mod }: { children; mod?: string }) => (
    <div className={'responsiveBox ' + String(mod)}>{children}</div>
);

export default function AppContainer() {
    const dispatch = useDispatch();
    const appStatus = useSelector(statusSelector);
    const appStateError = useSelector(errorSelector);

    useEffect(() => {
        dispatch(startPollAppState());
    }, []);

    useEffect(() => {
        if (appStatus) {
            if (appStatus === AppStatus.Active) {
                // dispatch(push('/products'));
                dispatch(fetchProducts());
                dispatch(fetchProfiles());
            } else if (appStatus === AppStatus.Uninstalled) {
                dispatch(push('/uninstall'));
            }
        }
        /*here will be check paid subscription plan*/
        return () => {};
    }, [appStatus]);

    return !appStatus ? (
        appStateError ? (
            <div>{(appStateError as any).message}</div>
        ) : (
            <GlobalLoadingOverlay visible />
        )
    ) : (
        <Frame>
            <Page fullWidth>
                {appStatus !== AppStatus.Active ? (
                    <NoAvailableApp status={appStatus} />
                ) : (
                    <>
                        <Box>
                            <TopNavBar />
                        </Box>
                        <Layout.Section oneHalf>
                            <DisplayText element="h2" size="large">
                                {/* TODO uncomment after AppBridge setup */}
                                {/* <RoutePropagator /> */}
                                <Switch>
                                    <Route path="/products">
                                        <Box mod="header">Products</Box>
                                    </Route>
                                    <Route path="/profiles">
                                        <Box mod="header">Profiles</Box>
                                    </Route>
                                </Switch>
                            </DisplayText>
                            <Switch>
                                <Route path="/" exact>
                                    <Redirect to="/products" />
                                </Route>
                                <Route path="/uninstall">
                                    <Uninstall />
                                </Route>
                                <Route path="/products">
                                    <ProductsList />
                                </Route>
                                <Route path="/profiles">
                                    <ProfilesList />
                                </Route>
                                <Route path="/settings">
                                    <AppSettings />
                                </Route>
                                <Route path="/subscription">
                                    <Subscription />
                                </Route>
                            </Switch>
                        </Layout.Section>
                    </>
                )}
                <Confirmation />
                <AppToast />
                <GlobalLoadingOverlay />
                <ToggleMock />
            </Page>
        </Frame>
    );
}
