import { AppStateResponse } from '@api/app';
import { isMocksLocalMode } from '@api/mocks/mock-env';
import { AppStatus } from '@api/types/app';
import { intl } from '@contexts/LocaleContext';
import { AnyAction, createSlice, PayloadAction } from '@reduxjs/toolkit';
import {
    startLoading,
    StateWithLoadingAndError,
    stopLoading,
} from './common/reducers';
import {
    bulkEnable,
    bulkEnableFailureByPlan,
    openAssignProfileModal,
    unassignProfile,
} from './products';
import { startUninstallPreparationSuccess } from './uninstall';
import getSymbolFromCurrency from 'currency-symbol-map';
import { format } from 'date-fns-tz';

interface ConfirmationActions {
    primary: string;
    secondary: string;
}

export enum CurrencyShopifyFormat {
    Amount = 'amount',
    AmountNoDecimals = 'amount_no_decimals',
    AmountWithCommaSeparator = 'amount_with_comma_separator',
    AmountNoDecimalsWithCommaSeparator = 'amount_no_decimals_with_comma_separator',
    AmountWithApostropheSeparator = 'amount_with_apostrophe_separator',
}

interface AppState extends StateWithLoadingAndError {
    mock: boolean;
    statePolling: boolean;
    status: AppStatus | null;
    money_format: string;
    currency_code: string;
    currency_symbol: string;
    currency_shopify_format: CurrencyShopifyFormat;
    timezone_offset: string;
    toast: null | {
        content: string;
        duration?: number;
        error?: boolean;
    };
    confirmation: {
        open: boolean;
        content: string;
        warning?: true;
        actions?: ConfirmationActions | false;
    };
    openedPopover: number | null;
}

const initialState: AppState = {
    loading: false,
    error: null,
    toast: null,
    mock: isMocksLocalMode,
    statePolling: false,
    status: null,
    money_format: '${amount}',
    currency_code: 'USD',
    currency_symbol: '$',
    currency_shopify_format: CurrencyShopifyFormat.Amount,
    timezone_offset: format(new Date(), 'XXX'),
    confirmation: {
        open: false,
        content: '',
        actions: {
            primary: 'Yes',
            secondary: 'No',
        },
    },
    openedPopover: null,
};

const appSlice = createSlice({
    name: 'app',
    initialState,
    reducers: {
        startLoading,
        stopLoading,
        toggleMock(state, { payload }: PayloadAction<boolean>) {
            state.mock = payload;
        },
        toggleConfirmation(
            state,
            action: PayloadAction<Partial<AppState['confirmation']>>
        ) {
            state.confirmation = {
                ...state.confirmation,
                ...action.payload,
                open: action.payload.open ?? true,
            };
        },
        toggleToast(
            state,
            action: PayloadAction<NonNullable<AppState['toast']> | undefined>
        ) {
            if (action.payload) {
                state.toast = {
                    ...state.toast,
                    ...action.payload,
                    duration: action.payload.duration ?? 5000,
                };
            } else {
                state.toast = initialState.toast;
            }
        },
        handlePrimaryConfirmationAction(state) {
            state.confirmation = initialState.confirmation;
        },
        handleSecondaryConfirmationAction(state) {
            state.confirmation = initialState.confirmation;
        },
        startPollAppState(state) {
            state.statePolling = true;
        },
        stopPollAppState(state) {
            state.statePolling = false;
        },
        getAppState(state) {
            state.loading = false;
        },
        getAppStateSuccess(
            state,
            { payload }: PayloadAction<AppStateResponse>
        ) {
            state.loading = false;
            state.status = payload.app_status;
            if (payload.money_format) {
                const match = /\{([\w_]*)\}/.exec(payload.money_format);

                if (match && match.length) {
                    state.money_format = payload.money_format;
                    state.currency_shopify_format = match[1] as CurrencyShopifyFormat;
                }
            }
            if (payload.currency_code) {
                state.currency_code = payload.currency_code;
            }
            if (payload.timezone_offset) {
                state.timezone_offset = payload.timezone_offset;
            }
            state.currency_symbol =
                getSymbolFromCurrency(payload.currency_code) ??
                state.currency_symbol;
            state.statePolling = [
                AppStatus.Active,
                AppStatus.Uninstalled,
            ].includes(payload.app_status);
        },
        getAppStateFailure(state, { payload }: PayloadAction<unknown>) {
            state.loading = false;
            state.error = (payload as string) || null;
            state.statePolling = false;
        },
        openPopover(state, { payload }: PayloadAction<number | null>) {
            state.openedPopover = payload;
        },
        closePopover(state, { payload }: PayloadAction<number | null>) {
            if (state.openedPopover === payload) {
                state.openedPopover = null;
            }
        },
    },
    extraReducers: (builder) => {
        builder
            .addCase(startUninstallPreparationSuccess, (state, action) => {
                state.status = AppStatus.PrepareToUninstallInProgress;
            })
            .addMatcher(
                (action: AnyAction): action is any =>
                    [openAssignProfileModal, unassignProfile, bulkEnable]
                        .map((item) => item.toString())
                        .includes(action.type),
                (state, action) => {
                    state.toast = initialState.toast;
                }
            );
    },
});

export const {
    startLoading: startAppLoading,
    stopLoading: stopAppLoading,
    toggleMock,
    toggleConfirmation,
    toggleToast,
    handlePrimaryConfirmationAction,
    handleSecondaryConfirmationAction,
    startPollAppState,
    stopPollAppState,
    getAppState,
    getAppStateSuccess,
    getAppStateFailure,
    openPopover,
    closePopover,
} = appSlice.actions;

export default appSlice.reducer;
