import {
    ActionCreatorWithPayload,
    Action,
    PayloadAction,
} from '@reduxjs/toolkit';
import { Component } from 'react';
import { components } from '../schema';
import * as Libs from '@libs/common/types';

export type Nullable<T> = Libs.Nullable<T>;

type schemas = components['schemas'];

export type Plan = schemas['Plans'];
// export type Profile = schemas['Profile'];

export enum PreorderStatus {
    Enabled = 'enabled',
    Disabled = 'disabled',
    Activated = 'activated',
}

interface EnableStatus {
    enabled: boolean;
}

export type ProductDTO = {
    id: string;
    preorder_status: PreorderStatus;
    title: string;
    bodyHtml: string;
    totalInventory: number;
    // images: {
    //     edges: {
    //         node: {
    //             id: string;
    //             src: string;
    //         };
    //     }[];
    // };
    images: {
        id: string;
        src: string;
    }[];
    productType: string;
    vendor: string;
    profile_id: string | null;
    updatedAt: string;
    createdAt: string;
    variants: Record<string, VariantDTO>;
    cursor: string;
    preorder_activating_start_date: null | string;
    preorder_activating_end_date: null | string;
};

export type ProductInState = Omit<ProductDTO, 'variants'> &
    EnableStatus & {
        variants: string[];
    };

export type Product = Omit<ProductInState, 'variants'> & {
    variants: Record<string, Variant>;
    different: boolean;
    differentProfile?: boolean;
};

export type VariantDTO = {
    id: string;
    price: string | null;
    price_shopify: string | null;
    sku: string;
    title: string;
    image: null | {
        id: string;
        src: string;
    };
    product_id: string;
    profile_id: string | null;
    preorder_status: PreorderStatus;
    preorder_activating_start_date: null | string;
    preorder_activating_end_date: null | string;
};

export type VariantInState = VariantDTO & EnableStatus;

export type Variant = VariantInState;
export type Settings = schemas['Settings'];

export enum PageDirection {
    Before = 'before',
    After = 'after',
    Same = 'same',
}

export interface PageInfoResponse {
    hasNextPage: boolean;
    hasPreviousPage: boolean;
}
export interface PageInfo extends PageInfoResponse {
    pageSize: number;
    pageDirection: PageDirection;
    cursor: string;
}

export enum SortField {
    Inventory = 'inventory',
    Title = 'title',
}

export enum SortDir {
    Asc = 'asc',
    Desc = 'desc',
}

export enum FilterStatus {
    Activated = 'activated',
    Scheduled = 'scheduled',
}

export enum FilterPriceType {
    Activated = 'activated',
    Scheduled = 'scheduled',
}

export enum FilterAvailiability {
    SoldOut = 'sold_out',
    InStock = 'in_stock',
    Preordered = 'preordered',
}

export enum PriceSettingsMode {
    Unstated = '',
    Same = 'same',
    Value = 'value',
    Increase = 'increase',
    Decrease = 'decrease',
}

export enum PriceType {
    SpecificValue = 'specific',
    Percent = 'relative_percent',
    Amount = 'relative_amount',
    Same = 'same',
}

export const Filter = {
    Status: 'status',
    Availiability: 'availiability',
    Query: 'query',
} as const;

const filterKeys = Object.values(Filter);
export type FilterKey = typeof filterKeys[number];
export type FilterValue = {
    filter: FilterKey;
    value: string | '';
};

export type FiltersState = {
    [Filter.Query]: string;
    [Filter.Status]: FilterStatus | '';
    [Filter.Availiability]: FilterAvailiability | '';
};

export type SortState = {
    dir: SortDir;
    field: SortField;
};

export type PayloadOf<A> = A extends ActionCreatorWithPayload<infer T>
    ? T
    : unknown;

export type ActionFrom<A> = PayloadAction<PayloadOf<A>>;

export type Defined<T> = T extends undefined ? never : T;

export interface UpdateItemsParams {
    items: {
        productId: string;
        variantId?: string;
    }[];
}

export interface AxiosClientError {
    message: string;
    code: string | undefined;
    data: any;
    status: number | undefined;
}
