import React from 'react';
import { AppStatus } from '@api/types/app';
import Uninstall from '@components/Uninstall/Uninstall';
import Progress from '@components/Uninstall/Steps/Progress/Progress';
import './NoAvailableApp.scss';

export function NoAvailableApp({ status }: { status: AppStatus }) {
    let content = <Uninstall />;

    switch (status) {
        case AppStatus.PrepareToUninstallInProgress:
            content = <Progress />;
            break;
        case AppStatus.DowngradeInProgress:
            content = (
                <Progress text="Price plan downgrade is in progress..." />
            );
            break;
        case AppStatus.Uninstalled:
            content = <Uninstall />;
            break;
    }
    return <div className="app-in-long-processing">{content}</div>;
}
