import { combineEpics } from 'redux-observable';
import * as appEpics from './app';
import * as productsEpics from './products';
import * as profilesEpics from './profiles';
import * as settingsEpics from './settings';
import * as subscriptionEpics from './subscription';
import * as uninstallEpics from './uninstall';
import { innerActionEpic } from './utils';

const featuredEpics = [
    appEpics,
    productsEpics,
    profilesEpics,
    settingsEpics,
    subscriptionEpics,
    uninstallEpics,
].flatMap((e) => Object.values(e));

export const utilsEpics = [innerActionEpic];

export const rootEpic = combineEpics<any, any, any, any>(
    ...utilsEpics,
    ...featuredEpics
);
