import React, { useCallback } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Checkbox } from '@shopify/polaris';
import { mockSelector } from '@redux/selectors/app';
import { isProduction } from '@api/mocks/mock-env';
import { toggleMock } from '@redux/reducers/app';
import { fetchProducts } from '@redux/reducers/products';
import { fetchProfiles } from '@redux/reducers/profiles';

export default function ToggleMock() {
    const dispatch = useDispatch();
    const mock = useSelector(mockSelector);

    const handleOnChange = useCallback((v) => {
        dispatch(toggleMock(v));
        if (!v) {
            dispatch(fetchProducts());
            dispatch(fetchProfiles());
        }
    }, []);
    return (
        <>
            {isProduction ? null : (
                <div className="mockToggler">
                    <Checkbox
                        label="Mock"
                        onChange={handleOnChange}
                        checked={mock}
                    />
                </div>
            )}
        </>
    );
}
