export const fetchProfilesEpic = (
    actions$: ActionsObservable<any>,
    state$: StateObservable<RootState>
) =>
    actions$.pipe(
        ofType<any>(fetchProfiles, fetchProfilesWithQuery),
        switchMap(() => {
            const state = state$.value.profiles;
            const filter = state.filters;
            const pageInfo = { ...state.pageInfo };
            const sort = state.sort;

            // if (
            //     pageInfo.pageDirection === PageDirection.After &&
            //     state.pageInfo.hasNextPage
            // ) {
            //     const idOfLastProfile =
            //         state.profilesIds[state.profilesIds.length - 1];
            //     pageInfo.cursor = state.profilesIds[idOfLastProfile].cursor;
            // } else if (
            //     pageInfo.pageDirection === PageDirection.Before &&
            //     state.pageInfo.hasPreviousPage
            // ) {
            //     const idOfFirstProduct = state.productsIds[0];
            //     pageInfo.cursor = state.productsById[idOfFirstProduct].cursor;
            // }

            return profilesApi
                .getProfiles({
                    filter,
                    pageInfo,
                    sort,
                })
                .pipe(
                    map((profiles) => getProfilesSuccess(profiles)),
                    catchError((err) => of(getProfilesFailure(err)))
                );
        })
    );

