export const assignProfileEpic = (
    actions$: ActionsObservable<any>,
    state$: StateObservable<RootState>
) =>
    actions$.pipe(
        ofType(assignProfile),
        switchMap((action: ActionFrom<typeof assignProfile>) => {
            const state = state$.value.products;
            const { id, item } = action.payload;
            const items: ApplyProfilesParams['items'] = [];

            const product = state.assignProfileModal.data?.product || item;
            if (product) {
                const { product_id, id } = product as Variant;
                if (product_id) {
                    items.push({ productId: product_id, variantId: id });
                } else {
                    items.push({
                        productId: id,
                    });
                    state.productsById[id].variants.forEach((variantId) => {
                        items.push({
                            productId: id,
                            variantId,
                        });
                    });
                }
            } else {
                state.selectedProducts.productsIds.forEach((productId) =>
                    items.push({
                        productId,
                    })
                );
                state.selectedProducts.variantsIds.forEach((variant_id) =>
                    items.push({
                        variantId: variant_id,
                        productId: state.variantsById[variant_id].product_id,
                    })
                );
            }

            const params: ApplyProfilesParams = {
                id,
                items,
            };

            return profilesApi.applyProfile(params).pipe(
                map(() => assignProfileSuccess(true)),
                catchError((err) => of(assignProfileFailure(err))),
                startWith(toggleBulkEnableWarning())
            );
        })
    );

