export const updateProfileEpic = (actions$: ActionsObservable<any>) =>
    actions$.pipe(
        ofType(updateProfile),
        switchMap((action: ActionFrom<typeof updateProfile>) => {
            return profilesApi.updateProfile(action.payload).pipe(
                map(() => updateProfileSuccess(true)),
                catchError((err) => of(updateProfileFailure(err)))
            );
        })
    );

export const updateProfileSuccessEpic = (actions$: ActionsObservable<any>) =>
    actions$.pipe(
        ofType(updateProfileSuccess),
        map(() => {
            return fetchProfiles();
        })
    );

export const fetchProfileEpic = (actions$: ActionsObservable<any>) =>
    actions$.pipe(
        ofType<any>(fetchProfile),
        switchMap((action: ActionFrom<typeof fetchProfile>) => {
            return profilesApi.getProfile(action.payload).pipe(
                map((settings) => fetchProfileSuccess(settings)),
                catchError((err) => of(fetchProfileFailure(err)))
            );
        })
    );

export const fetchProfilesWithQueryEpic = (actions$: ActionsObservable<any>) =>
    actions$.pipe(
        ofType(fetchProfilesWithQuery),
        debounceTime(600),
        map(fetchProfilesWithQueryStart)
    );

export const getDefaultSettingsEpic = (actions$: ActionsObservable<any>) =>
    actions$.pipe(
        ofType<any>(getDefaultSettingsForProfile),
        switchMap(() => {
            return settingsApi.getSettings().pipe(
                map((settings) => getSettingsSucces(settings)),
                catchError((err) => of(getSettingsFailure(err)))
            );
        })
    );

export const deleteProfileEpic = (actions$: ActionsObservable<any>) =>
    actions$.pipe(
        ofType(deleteProfile),
        withConfirmation<typeof deleteProfile>(
            () => ({
                content: `Are you sure you want to delete this profile?`,
                actions: {
                    primary: 'Yes',
                    secondary: 'Cancel',
                },
            }),
            (action) =>
                profilesApi.deleteProfile(action.payload).pipe(
                    map(() => deleteProfileSuccess(true)),
                    catchError((err: AxiosClientError) => {
                        //TODO remove after workflow approvement
                        //     if (err.status === 403) {
                        //         return Array.from([
                        //             toggleConfirmation({
                        //                 content: `This profile is assigned to active pre-orders. To delete this profile, please end all pre-orders this profile is assigned to.`,
                        //                 actions: false,
                        //                 warning: true,
                        //             }),
                        //             stopLoadingProfiles(),
                        //         ]);
                        //     }
                        //     return of(deleteProfileFailure(err));
                        return of(stopLoadingProfiles());
                    }),
                    startWith(startLoadingProfiles())
                )
            // actions$
        )
    );

export const deleteProfileFailureEpic = (actions$: ActionsObservable<any>) =>
    actions$.pipe(
        ofType<any>(deleteProfileFailure),
        filter<ActionFrom<typeof deleteProfileFailure>>(
            (action) => action.payload.status === 400
        ),
        withConfirmation<typeof deleteProfileFailure>(
            () => ({
                content: `This profile is assigned to active pre-orders. To delete this profile, please end all pre-orders this profile is assigned to.`,
            })
            // actions$
        )
    );

export const deleteProfileSuccessEpic = (actions$: ActionsObservable<any>) =>
    actions$.pipe(
        ofType(deleteProfileSuccess),
        map(() => {
            return fetchProfiles();
        })
    );
