import React from 'react';
import { Button } from '@shopify/polaris';
import { authHeadersSelector } from '@redux/selectors/auth';
import { useSelector } from 'react-redux';

function Complition() {
    const authHeaders = useSelector(authHeadersSelector);

    const handleOnClick = React.useCallback(() => {
        const { 'X-Shop-Domain': host } = authHeaders;
        const shopName = host?.split('.')[0];
        const applicationId = '';

        window.open(
            `https://${
                shopName ?? ''
            }.myshopify.com/admin/applications#application-${applicationId}`,
            'blank'
        );
    }, [authHeaders]);

    return (
        <div className="appUninstall__complition">
            <div className="appUninstall__step__content">
                Preparation for application removal is done
            </div>
            <Button onClick={handleOnClick}>
                Delete App from Shopify admin panel &gt;&gt;
            </Button>
        </div>
    );
}

export default Complition;
