import { ActionsObservable, ofType, StateObservable } from 'redux-observable';
import { from, of, timer } from 'rxjs';
import {
    catchError,
    exhaustMap,
    map,
    switchMap,
    takeUntil,
} from 'rxjs/operators';
import uninstallApi from '@api/uninstall';
import { withAppLoading } from './utils';
import {
    getUninstallStatus,
    getUninstallStatusSuccess,
    getUninstallStatusFailure,
    startPollUninstallStatus,
    startUninstallPreparation,
    startUninstallPreparationFailure,
    startUninstallPreparationSuccess,
    stopPollUninstallStatus,
} from '@redux/reducers/uninstall';
import { RootState } from '@redux/reducers/rootReducer';
import { UninstallStatus } from '@api/types/uninstall';

export const getUnistallStatusEpic = (
    actions$: ActionsObservable<any>,
    state$: StateObservable<RootState>
) =>
    actions$.pipe(
        ofType<any>(getUninstallStatus),
        exhaustMap(() => {
            return uninstallApi.getUninstallStatus().pipe(
                switchMap((response) => {
                    const actions: any = [getUninstallStatusSuccess(response)];
                    if (
                        response.prepare_to_uninstall_status !==
                        UninstallStatus.Progress
                    ) {
                        actions.push(stopPollUninstallStatus());
                    }

                    return from(actions);
                }),
                catchError((err) =>
                    from([
                        getUninstallStatusFailure(err),
                        stopPollUninstallStatus(),
                    ])
                )
            );
        })
    );

export const pollUnistallStatusEpic = (actions$: ActionsObservable<any>) =>
    actions$.pipe(
        ofType<any>(startPollUninstallStatus),
        switchMap(() =>
            timer(0, 5000).pipe(
                takeUntil(actions$.ofType(stopPollUninstallStatus)),
                map(() => getUninstallStatus())
            )
        )
    );
export const startUnistallPreparationEpic = (
    actions$: ActionsObservable<any>
) =>
    actions$.pipe(
        ofType<any>(startUninstallPreparation),
        withAppLoading(() => {
            return uninstallApi.startUnistallPreparation().pipe(
                map(() => startUninstallPreparationSuccess()),
                catchError((err) => of(startUninstallPreparationFailure(err)))
            );
        })
    );

