import React from 'react';
import { useSelector } from 'react-redux';
import { Spinner } from '@shopify/polaris';
import { isLoadingAppSelector } from '@redux/selectors/app';
import LoadingOverlay from '@components/ui-kit/LoadingOverlay/LoadingOverlay';

type Props = {
    visible?: boolean;
};

const GlobalLoadingOverlay: React.FC<Props> = ({ visible }) => {
    const loading = useSelector(isLoadingAppSelector);

    return !visible && !loading ? null : (
        <LoadingOverlay>
            <Spinner />
        </LoadingOverlay>
    );
};

export default GlobalLoadingOverlay;
