type Locale = Pick<IntlConfig, 'messages' | 'locale'>;

type LocaleContextType = {
    config: Locale;
    setLocale: (local: Locale) => void;
};

export class NumberParser {
    _group: string;
    get groupSeparator() {
        return this._group;
    }
    _groupRegExp: RegExp;

    _decimal: string;
    get decimalSeparator() {
        return this._decimal;
    }
    _decimalRegExp: RegExp;

    _numeral: string;
    _numeralRegExp: RegExp;
    _getIndex: (v: string) => string;

    constructor(locale) {
        const parts = new Intl.NumberFormat(locale).formatToParts(12345.6);
        const intl = new Intl.NumberFormat(locale, { useGrouping: false });
        const numerals = [...intl.format(9876543210).split('')].reverse();

        const index = new Map(numerals.map((d, i) => [d, i]));
        this._group = parts.find((d) => d.type === 'group')!.value;

        this._groupRegExp = new RegExp(`[${this._group}]`, 'g');
        this._decimal = parts.find((d) => d.type === 'decimal')!.value;
        this._decimalRegExp = new RegExp(
            `[${parts.find((d) => d.type === 'decimal')!.value}]`
        );
        this._numeral = numerals.join('');
        this._numeralRegExp = new RegExp(`[${this._numeral}]`, 'g');
        this._getIndex = (d) => String(index.get(d));
    }

    tryParse(value: string): string | false {
        const replacedValue = value
            .trim()
            .replace(/^0+/, '')
            .replace(this._groupRegExp, '')
            .replace(this._decimalRegExp, '.')
            .replace(this._numeralRegExp, this._getIndex);

        const result = Number(replacedValue);
        return isNaN(result) ? false : result.toString();
    }
}

// eslint-disable-next-line @typescript-eslint/no-unsafe-call, @typescript-eslint/no-unsafe-assignment
enLocaleConfig.messages = flatten(English);

const cache = createIntlCache();

export const intl = createIntl(enLocaleConfig, cache);

const LocaleContext = React.createContext<LocaleContextType>({
    config: intl,
    setLocale: (local: Locale) => void local,
});

const LocaleProvider: React.FC = (props) => {
    const [config, setLocale] = useState<Locale>(enLocaleConfig);

    const —Åontext = {
        config,
        setLocale,
    };
    return <LocaleContext.Provider value={—Åontext} {...props} />;
};

function useLocale() {
    const context = React.useContext(LocaleContext);
    if (context === undefined) {
        throw new Error(`useUser must be used within a UserProvider`);
    }
    return context;
}

type Dictionary<T> = { [key in keyof T]: MessageDescriptor };

function formatDictionary<T extends Dictionary<T>>(
    dic: T,
    fmt: (...args: any) => string
) {
    return Object.keys(dic).reduce(
        (acc, key) => ({ [key]: fmt(dic[key as keyof T]), ...acc }),
        {} as { [key in keyof T]: string }
    );
}

function useFormat() {
    // eslint-disable-next-line @typescript-eslint/unbound-method
    const { formatMessage, formatDate } = useIntl();
    const formatDictionaryCb = useCallback(
        <T extends Dictionary<T>>(dic: T) =>
            formatDictionary<T>(dic, formatMessage),
        [formatMessage]
    );
    const moneyFormat = useSelector(moneyFormatSelector);

    const moneyFormatMessage = React.useMemo(
        () =>
            defineMessage({
                id: 'app.intl.moneyFormat',
                defaultMessage: moneyFormat.money_format,
            }),
        [moneyFormat.money_format]
    );

    const twoFractionDigitsOpts = {
        minimumFractionDigits: 2,
        maximumFractionDigits: 2,
    };
    const noFractionDigitsOpts = { maximumFractionDigits: 0 };

    const getIntlOptions = useCallback(() => {
        const intl = {
            [CurrencyShopifyFormat.Amount]: {
                locale: 'en-US',
                options: twoFractionDigitsOpts,
            },
            [CurrencyShopifyFormat.AmountNoDecimals]: {
                locale: 'en-US',
                options: noFractionDigitsOpts,
            },
            [CurrencyShopifyFormat.AmountWithCommaSeparator]: {
                locale: 'de-GE',
                options: twoFractionDigitsOpts,
            },
            [CurrencyShopifyFormat.AmountNoDecimalsWithCommaSeparator]: {
                locale: 'de-GE',
                options: noFractionDigitsOpts,
            },
            [CurrencyShopifyFormat.AmountWithApostropheSeparator]: {
                locale: 'de-CH',
                options: twoFractionDigitsOpts,
            },
        }[moneyFormat.currency_shopify_format] as {
            locale: string;
            options: FormatNumberOptions;
        };

        return intl;
    }, [moneyFormat.currency_shopify_format]);

    const formatCurrencyValue = useCallback(
        (amount: string, options?: FormatNumberOptions) => {
            const intl = getIntlOptions();

            amount = createIntl({ locale: intl.locale }).formatNumber(
                Number(amount),
                {
                    ...intl.options,
                    ...options,
                }
            );

            return amount;
        },
        [getIntlOptions]
    );

    const formatMoney = useCallback(
        (amount: string) => {
            amount = formatCurrencyValue(amount);

            return formatMessage(moneyFormatMessage, {
                [moneyFormat.currency_shopify_format]: amount,
            });
        },
        [
            formatMessage,
            moneyFormatMessage,
            moneyFormat.currency_shopify_format,
            formatCurrencyValue,
        ]
    );

    // const getFormattedPriceString = useCallback(
    //     (amount: string) =>
    //         formatMessage(moneyFormatMessage, {
    //             amount,
    //         }),
    //     [formatMessage, moneyFormatMessage]
    // );

    const getPriceString = useCallback(
        (amount: string | number | null) =>
            getFormattedPriceString(formatMoney, amount),
        []
    );

    const formatters = useMemo(
        () => ({
            fmt: formatMessage,
            formatDictionary: formatDictionaryCb,
            formatMessage,
            formatDate,
            formatCurrencyValue,
            formatMoney,
            getPriceString,
            getIntlOptions,
        }),
        [
            formatMessage,
            formatDictionaryCb,
            formatDate,
            formatMoney,
            getIntlOptions,
            formatCurrencyValue,
            getPriceString,
        ]
    );

    return formatters;
}

type Message = {
    id: string;
    defaultMessage: string;
};

function defineMessagesWithPrefix<
    T extends Message,
    U extends Record<string, T>
>(prefix: string, msgs: U): U {
    Object.keys(msgs).forEach((key: keyof U) => {
        const msg = msgs[key];

        msgs[key] = {
            ...msgs[key],
            id: `${prefix}.${msg.id}`,
        };
    });

    // return defineMessages(msgs);
    return msgs;
}

export {
    defineMessagesWithPrefix,
    useLocale,
    LocaleProvider,
    LocaleContext,
    useFormat,
};
