import {
    AppSettingsDTO,
    AppSettingsInState,
    Area,
    BaseDesignSettingsDTO,
    ButtonPart,
    CommonDesignSettingsDTO,
} from '@api/types/settings';
import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { StateWithLoadingAndError } from './common/reducers';

interface ConfirmationActions {
    primary: '12px';
    secondary: '12px';
}

type AppSettingsState = AppSettingsInState & StateWithLoadingAndError;

const defaultDesignSettings: BaseDesignSettingsDTO = {
    color: '#FFFFFF',
    background_color: '#6371c7',
    border_color: '#3f4eae',
    font_family: 'inherit',
    font_size: '18px',
    font_weight: 'normal',
    text_decoration: 'none',
    font_style: 'normal',
    border_radius: '3px',
    border_width: '1px',
    padding_top: '12px',
    padding_bottom: '12px',
    padding_right: '30px',
    padding_left: '30px',
};

const initialState: AppSettingsState = {
    loading: false,
    error: null,
    settings: {
        app: null,
        design: {
            selected: {
                preorder: 'button',
                checkout: 'button',
            },
            state: null,
        },
    },
    // design_settings: {
    //     preorder_button: defaultDesignSettings,
    //     checkout_button: defaultDesignSettings,

    //     user_id: '',
    //     created_at: '',
    //     updated_at: '',
    // },
    // app_settings: {
    //     user_id: '',
    //     price: '',
    //     price_type: 'specific',
    //     start_date: '',
    //     end_date: '',
    //     inventory_limit: '1',
    //     button_text: '',
    //     button_message: '',
    //     created_at: '',
    //     updated_at: '',
    // },
};

const slice = createSlice({
    name: 'settings',
    initialState,
    reducers: {
        resetSettings(state) {
            return initialState;
        },
        resetDesignSettings(
            {
                settings: {
                    design: { state, selected },
                },
            },
            { payload: area }: PayloadAction<Area>
        ) {
            if (state) {
                state.current[area] = state.default[area];
            }
        },
        setSelected(
            state,
            {
                payload: { area, part },
            }: PayloadAction<{ area: Area; part: ButtonPart }>
        ) {
            state.settings.design.selected[area] = part;
        },
        getSettings(state) {
            state.loading = true;
        },
        getSettingsSucces(state, { payload }: PayloadAction<AppSettingsDTO>) {
            state.settings.app = {
                ...initialState.settings.app,
                ...payload.app_settings,
            };
            state.settings.design = {
                ...initialState.settings.design,
                state: payload.design_settings,
            };
            // {
            //     ...state.design_settings,
            //     ...payload.design_settings,
            //     preorder_button: {
            //         ...initialState.design_settings?.preorder_button,
            //         ...payload.design_settings.preorder_button,
            //     },
            //     checkout_button: {
            //         ...initialState.design_settings?.checkout_button,
            //         ...payload.design_settings.checkout_button,
            //     },
            // };
            state.loading = false;
        },
        getSettingsFailure(state, { payload }: PayloadAction<AppSettingsDTO>) {
            state.loading = false;
        },
        patchAppSettings(
            state,
            { payload }: PayloadAction<AppSettingsInState['settings']['app']>
        ) {
            state.settings.app = payload;
        },
        patchAreaDesignSettingsInState(
            {
                settings: {
                    design: { state, selected },
                },
            },
            {
                payload: { style, area },
            }: PayloadAction<{
                style: Partial<CommonDesignSettingsDTO>;
                area: Area;
            }>
        ) {
            const areaInState = state!.current[area];
            areaInState[selected[area]] = {
                ...areaInState[selected[area]],
                ...style,
            };
        },
        patchCheckoutDesignSettingsInState(
            {
                settings: {
                    design: {
                        state,
                        selected: { checkout: selected },
                    },
                },
            },
            { payload }: PayloadAction<Partial<CommonDesignSettingsDTO>>
        ) {
            state!.current.checkout[selected] = {
                ...state!.current.checkout[selected],
                ...payload,
            };
        },
        // patchDesignSettings(
        //     state,
        //     { payload }: PayloadAction<AppSettingsInState['design_settings']>
        // ) {
        //     state.design_settings = payload;
        // },
        updateSettings(state) {
            state.loading = true;
        },
        updateSettingsSuccess(state, { payload }: PayloadAction<boolean>) {
            state.loading = false;
        },
        updateSettingsFailure(state, { payload }: PayloadAction<any>) {
            state.loading = false;
        },
    },
    extraReducers: () => {
        // builder.addCase(openSettingsModal, (state, action) => {
        //     state.profileModal = initialState.profileModal;
        //, });
    },
});

export const {
    resetSettings,
    resetDesignSettings,
    setSelected,
    getSettings,
    getSettingsSucces,
    getSettingsFailure,
    patchAppSettings,
    updateSettings,
    updateSettingsSuccess,
    updateSettingsFailure,
    patchAreaDesignSettingsInState,
    patchCheckoutDesignSettingsInState,
} = slice.actions;

export default slice.reducer;
