import { SubscriptionResponse } from '@api/subscription';
import { SubscriptionDTO } from '@api/types/subscription';
import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { ClientApplication } from '@shopify/app-bridge/client/types';
import { StateWithLoadingAndError } from './common/reducers';

interface ConfirmationActions {
    primary: string;
    secondary: string;
}

interface SubscriptionState extends StateWithLoadingAndError {
    subscription: {
        items: SubscriptionResponse;
        active: SubscriptionDTO | null;
    };
}

const initialState: SubscriptionState = {
    loading: false,
    error: null,
    subscription: { items: [], active: null },
};

const subscriptionSlice = createSlice({
    name: 'subscription',
    initialState,
    reducers: {
        switchToPlan(
            state,
            {
                payload,
            }: PayloadAction<{
                id: number;
                app?: ClientApplication<any>;
                withConfirmation?: boolean;
            }>
        ) {
            state.loading = true;
        },
        getSubscription(state) {
            state.loading = true;
        },
        getSubscriptionSuccess(
            state,
            { payload }: PayloadAction<SubscriptionResponse>
        ) {
            state.subscription.items = payload;
            state.loading = false;
        },
        getSubscriptionFailure(state, { payload }: PayloadAction<unknown>) {
            state.loading = false;
            state.error = (payload as null) || null;
        },
        getActiveSubscription(state) {
            state.loading = true;
        },
        getActiveSubscriptionSuccess(
            state,
            { payload }: PayloadAction<SubscriptionDTO>
        ) {
            state.subscription.active = {
                ...payload,
                terms:
                    'FREE for 3 Products. Then just $0.99 per month after that.',
            };
            state.loading = false;
        },
        getActiveSubscriptionFailure(
            state,
            { payload }: PayloadAction<unknown>
        ) {
            state.loading = false;
            state.error = (payload as null) || null;
        },
    },
    extraReducers: () => {
        // builder.addCase(openSettingsModal, (state, action) => {
        //     state.profileModal = initialState.profileModal;
        //, });
    },
});

export const {
    switchToPlan,
    getSubscription,
    getSubscriptionSuccess,
    getSubscriptionFailure,
    getActiveSubscription,
    getActiveSubscriptionSuccess,
    getActiveSubscriptionFailure,
} = subscriptionSlice.actions;

export default subscriptionSlice.reducer;
