import React from 'react';
import { Stack, Button, Layout } from '@shopify/polaris';
import { useFormat } from '@contexts/LocaleContext';
import messages from './messages';
import { push } from 'connected-react-router';

import { ModalSettingsProps } from '@components/Products/ModalProductSettings/ModalProductSettingsParts';
import ModalCreateProfile from '@components/Profiles/ModalCreateProfile/ModalCreateProfile';
import { useDispatch } from 'react-redux';
import { TitleBar } from '@shopify/app-bridge-react';
import { ActionProps } from '@shopify/app-bridge-react/types';

function TopNavBar() {
    const dispatch = useDispatch();
    const { formatDictionary } = useFormat();
    const msg = formatDictionary(messages);

    const secondaryActions: ActionProps[] = [
        {
            content: msg.buttonsProducts,
            onAction: () => dispatch(push('/products')),
        },
        {
            content: msg.buttonsProfiles,
            onAction: () => dispatch(push('/profiles')),
        },
        {
            content: msg.buttonsKnowledgeBase,
            url: 'https://appbossapps.freshdesk.com/support/solutions',
            external: true,
        },
    ];
    const actionGroups = [
        {
            title: msg.buttonsMore,
            actions: [
                {
                    content: msg.buttonsSettings,
                    onAction: () => dispatch(push('/settings')),
                },
                {
                    content: msg.buttonsSubscription,
                    onAction: () => dispatch(push('/subscription')),
                },
            ],
        },
    ];

    const onClickCreator = (url: string) => () => {
        dispatch(push(url));
    };

    // TODO remove after AppBridge setup will be finished
    return false ? (
        <TitleBar
            title=""
            // title="My App using up-to-date code"
            breadcrumbs={[]}
            secondaryActions={secondaryActions}
            actionGroups={actionGroups}
        />
    ) : (
        <Layout.Section>
            {/*Top Navigation Section*/}
            <div id="topNav">
                <Stack>
                    {/* <Button>{msg.buttonsDashboard}</Button> */}
                    <Button onClick={onClickCreator('/products')}>
                        {msg.buttonsProducts}
                    </Button>
                    <Button onClick={onClickCreator('/profiles')}>
                        {msg.buttonsProfiles}
                    </Button>
                    <Button
                        url="https://appbossapps.freshdesk.com/support/solutions"
                        external
                    >
                        {msg.buttonsKnowledgeBase}
                    </Button>
                    <Button
                        connectedDisclosure={{
                            accessibilityLabel: msg.buttonsDesign,
                            actions: [
                                // {
                                //     content: msg.buttonsProfiles,
                                //     onAction: () => dispatch(push('/profiles')),
                                // },
                                {
                                    content: msg.buttonsSettings,
                                    onAction: () => dispatch(push('/settings')),
                                },
                                // {
                                //     content: msg.buttonsKnowledgeBase,
                                //     onAction: () =>
                                //         window.open(
                                //             'https://appbossapps.freshdesk.com/support/solutions',
                                //             'blank'
                                //         ),
                                // },
                                {
                                    content: msg.buttonsSubscription,
                                    onAction: () =>
                                        dispatch(push('/subscription')),
                                },
                            ],
                        }}
                    >
                        {msg.buttonsMore}
                    </Button>
                    {/* <Button url="/uninstall">{msg.buttonsUninstall}</Button> */}
                </Stack>
            </div>
        </Layout.Section>
    );
}
export default TopNavBar;
