import { Product, Variant } from '@api/types';
import store from '@redux/store';

export const isDefaultVariant = (variant: { title: string }) => {
    // const regxp = new RegExp('(w*Default Title|testw*)', 'g');
    // const if_vars = !regxp.test(prod_var.title);
    return variant.title.includes('Default Title');
};

export const getProductUrl = (item: Product | Variant, isVariant?: true) => {
    const headers = store.getState().auth.headers;
    const domain = headers['X-Shop-Domain'];
    let url = `https://${domain}`;

    if (isVariant) {
        const { id, product_id } = item as Variant;
        url += `/admin/products/${product_id}/variants/${id}`;
    } else {
        const { id } = item;
        url += `/admin/products/${id}`;
    }

    return url;
};

export const getFormattedPriceString = (
    fmt: (amount: string) => string,
    price?: string | number | null
) => {
    let result: string = '';
    if (price || price === 0) {
        price = parseFloat(String(price)).toFixed(2);
        result = fmt(price);
    }

    return result;
};

export const isNullOrUndefined = (value?: string | number | null) => {
    return value === null || value === undefined;
};
