export const fetchProductsEpic = (
    actions$: ActionsObservable<any>,
    state$: StateObservable<RootState>
) =>
    actions$.pipe(
        ofType<any>(
            fetchProducts,
            fetchProductsWithQueryStart,
            setSort,
            setPage
        ),
        switchMap(() => {
            const state = state$.value.products;
            const filter = state.filters;
            const pageInfo = { ...state.pageInfo };
            const sort = state.sort;

            return productsApi
                .getProducts({
                    filter,
                    pageInfo,
                    sort,
                })
                .pipe(
                    map((products) => getProductsSuccess(products)),
                    catchError((err) => of(getProductsFailure(err)))
                );
        })
    );

export const fetchProductsWithQueryEpic = (actions$: ActionsObservable<any>) =>
    actions$.pipe(
        ofType(fetchProductsWithQuery),
        debounceTime(600),
        map(fetchProductsWithQueryStart)
    );

export const updateProductsAndVariantsStatusBulkEpic = (
    actions$: ActionsObservable<any>,
    state$: StateObservable<RootState>
) =>
    actions$.pipe(
        ofType(updateProductsAndVariantsStatusBulk),
        switchMap(
            (
                action: ActionFrom<typeof updateProductsAndVariantsStatusBulk>
            ) => {
                const { products, variants, enabled } = action.payload;

                const status = enabled
                    ? PreorderStatus.Enabled
                    : PreorderStatus.Disabled;

                const items: UpdateStatusBulkParams['items'] = [];

                for (const variant of variants) {
                    items.push({
                        productId: variant.product_id,
                        variantId: variant.id,
                    });
                }

                for (const product of products) {
                    items.push({
                        productId: product.id,
                    });
                }

                return productsApi
                    .updateProductsAndVariantsStatusBulk({ items, status })
                    .pipe(
                        switchMap((result) =>
                            result === false
                                ? from([
                                    bulkEnableFailureByPlan(),
                                    fetchProducts(),
                                ])
                                : of(setPage(PageDirection.Same))
                        ),
                        catchError(() => of(fetchProducts()))
                    );
            }
        )
    );

export const setStatusEpic = (
    actions$: ActionsObservable<any>,
    state$: StateObservable<RootState>
) =>
    actions$.pipe(
        ofType(setStatus),
        map((action: ActionFrom<typeof setStatus>) => {
            const { id, enabled, isVariant } = action.payload;
            const state = state$.value.products;

            type Payload = ActionFrom<
                typeof updateProductsAndVariantsStatusBulk
            >['payload'];

            const payload: Payload = {
                enabled,
                products: [],
                variants: [],
            };

            if (isVariant) {
                const variant = state.variantsById[id];
                payload.variants.push(variant);

                const product = state.productsById[variant.product_id];

                let allSame = true;
                product.variants.forEach((variantId) => {
                    if (id !== variantId) {
                        const variant = state.variantsById[variantId];
                        allSame = allSame && variant.enabled === enabled;
                    }
                });

                if (
                    (allSame && enabled) ||
                    (!allSame && !enabled && product.enabled)
                ) {
                    payload.products.push(product);
                }
            } else {
                const product = state.productsById[id];
                payload.products.push(product);

                payload.variants.push(
                    ...product.variants.map((id) => state.variantsById[id])
                );
                // product.enabled = enabled; //getPreorderEnableStatus(product);
                // product.variants.forEach((variantId) => {
                //     const variant = state.variantsById[variantId];
                //     // variant.enabled = enabled;
                // });
            }

            return updateProductsAndVariantsStatusBulk(payload);
        })
    );

export const setStatusSuccessEpic = (actions$: ActionsObservable<any>) =>
    actions$.pipe(
        ofType(setSettingsSuccess),
        map(() => {
            return setPage(PageDirection.Same);
        })
    );

export const setSettingsEpic = (
    actions$: ActionsObservable<any>,
    state$: StateObservable<RootState>
) =>
    actions$.pipe(
        ofType(setSettings),
        switchMap(({ payload }: ActionFrom<typeof setSettings>) => {
            const state = state$.value.products;
            const item = payload.items[0];

            if (!item.variantId) {
                const variants: typeof payload.items = state.productsById[
                    item.productId
                ].variants.map((variantId) => ({
                    productId: item.productId,
                    variantId,
                }));
                payload.items.push(...variants);
            }

            return productsApi.updateProductsSettings(payload).pipe(
                map(() => setSettingsSuccess(true)),
                // mapWithToast(messages.setSettingsSuccess, () =>
                //     setSettingsSuccess(true)
                // ),
                catchError((err) => of(setSettingsFailure(err)))
            );
        })
    );

export const fetchProductsSettingsEpic = (actions$: ActionsObservable<any>) =>
    actions$.pipe(
        ofType<any>(fetchProductSettings),
        switchMap((action: ActionFrom<typeof fetchProductSettings>) => {
            return productsApi.getProductSettings(action.payload).pipe(
                map((settings) => getProductSettingsSuccess(settings)),
                catchError((err) => of(getProductSettingsFailure(err)))
            );
        })
    );

export const bulkEnableEpic = (
    actions$: ActionsObservable<any>,
    state$: StateObservable<RootState>
) =>
    actions$.pipe(
        ofType(bulkEnable),
        withConfirmation<typeof bulkEnable>(
            (action) => ({
                content: `Are you sure you want to ${action.payload === undefined ? 'enable' : 'disable'
                    } selected items ? `,
            }),
            (action) => {
                const state = state$.value.products;
                const variantsItems = state.selectedProducts.variantsIds.map(
                    (id) => ({
                        productId: state.variantsById[id].product_id,
                        variantId: id,
                    })
                );

                const productItems = state.selectedProducts.productsIds.map(
                    (productId) => ({ productId })
                );

                const items = [...productItems, ...variantsItems];

                const status =
                    action.payload === undefined
                        ? PreorderStatus.Enabled
                        : PreorderStatus.Disabled;

                return productsApi
                    .updateProductsAndVariantsStatusBulk({
                        items,
                        status,
                    })
                    .pipe(
                        switchMap((result) =>
                            result === false
                                ? from([
                                    bulkEnableFailureByPlan(),
                                    toggleToast(),
                                    fetchProducts(),
                                ])
                                : of(bulkEnableSuccess(true))
                        ),
                        catchError((err) => of(bulkEnableFailure(err))),
                        startWith(
                            startLoadingProducts(),
                            toggleBulkEnableWarning()
                        )
                    );
            }
        )
    );

