import { createSlice, PayloadAction } from '@reduxjs/toolkit';
import { createApp } from '@shopify/app-bridge';

const url = new URL(window.location.href);
const search = url.searchParams;

const initialState = {
    apiKey: search.get('api_key'),
    originalHeaders: Array.from(search.entries()),
    headers: {
        'X-Shop-Signature': search.get('hmac'), // X-Shop-Signature   - hmac
        'X-Shop-Ses': search.get('session'), // X-Shop-Ses - session id
        'X-Shop-Time': search.get('timestamp'), // X-Shop-Time - timestamp
        'X-Shop-Domain': search.get('shop'), // X-Shop-Domain - shop domain
        'X-Shop-Locale': search.get('locale'), // X-Shop-Locale - locale
        'X-Shop-lng': search.get('new_design_language'), // X-Shop-lng - language
        'X-Shop-Host': search.get('host'), // X-Shop-host - host token ???
    },
};

