if (false) {
    // if (window.top == window.self) {
    // window.location.assign(permissionUrl);

    const config = {
        // apiKey: store.getState().auth.apiKey ?? '',
        apiKey: '49e2006401f6ab93d445cb5c74b51bc0',
        // apiKey: 'd53d160d30d3cde82887670b2c06188b',
        shopOrigin: search.get('shop') || '',
        // forceRedirect: true,
    };

    const redirectUri = window.location.origin;

    const permissionUrl =
        'https://' +
        config.shopOrigin +
        '/admin' +
        '/oauth/authorize?client_id=' +
        config.apiKey +
        '&scope=read_products,read_content&redirect_uri=' +
        redirectUri;

    // createApp(config);
    window.location.assign(permissionUrl);
    // If the current window is the 'child', change the parent's URL with Shopify App Bridge's Redirect action
}
// }

const authSlice = createSlice({
    name: 'auth',
    initialState,
    reducers: {
        setHeaders(state, action: PayloadAction<typeof initialState.headers>) {
            state.headers = action.payload;
        },
    },
});

export const { setHeaders } = authSlice.actions;

export default authSlice.reducer;
