const startUnistallPreparation = (): Observable<boolean> => {
    const url = `${prefix}/prepare`;

    if (isMocksMode()) {
        return of(true).pipe(delay(1800));
    }

    return axios.post<boolean>(url).pipe(map((res) => res.data));
};

