import { Observable, of } from 'rxjs';
import { delay, map } from 'rxjs/operators';
import axios from './axios';
import isMocksMode from './mocks/mock-env';
import profilesList from './mocks/profiles/profilesList';
import { ProductSettingsDTO, ProfileDTO } from './types';
import {
    FiltersState,
    PageInfo,
    PageInfoResponse,
    SortState,
    UpdateItemsParams,
} from './types/common';

// const prefix = '/profiles';
// export interface ProfilesResponse {
//     profiles: ProfileDTO[];
//     pageInfo: PageInfoResponse;
// }
const prefix = '/profiles';
// export interface ProfilesResponse {
//     profiles: ProfileDTO[];
//     pageInfo: PageInfoResponse;
// }
export type ProfilesResponse = ProfileDTO[];
export type ProfileResponse = ProfileDTO;

interface GetProfilesParams {
    filter: FiltersState;
    pageInfo: PageInfo;
    sort: SortState;
}

const updateProfile = (params: ProfileDTO): Observable<ProfileDTO> => {
    const { id, ...data } = params;
    let url = `${prefix}`;
    const method = axios.post.bind(axios);

    if (id) {
        url += `/${id}`;
    }

    // eslint-disable-next-line  no-constant-condition
    return method<ProfileDTO>(url, data).pipe(map((res) => res.data));
};

export interface ApplyProfilesParams extends UpdateItemsParams {
    id: string;
}

const applyProfile = (params: ApplyProfilesParams): Observable<ProfileDTO> => {
    const { id, ...data } = params;
    const url = `${prefix}/${id}/apply`;

    if (isMocksMode()) {
        return of(profilesList[0]).pipe(delay(800));
    }

    // eslint-disable-next-line  no-constant-condition
    return axios.post<ProfileDTO>(url, data).pipe(map((res) => res.data));
};

const unassignProfile = (
    params: ApplyProfilesParams
): Observable<ProfileDTO> => {
    const { id, ...data } = params;
    const url = `${prefix}/unassign`;

    // if (isMocksMode()) {
    //     return of(profilesList[0]).pipe(delay(800));
    // }

    return axios.post<ProfileDTO>(url, data).pipe(map((res) => res.data));
};

const getProfiles = (params: GetProfilesParams) => {
    const url = `${prefix}`;
    const searchParams = new URLSearchParams();
    // searchParams.set('filter', params.filter.availiability);
    if (params.filter.query) {
        searchParams.set('name', params.filter.query);
    }


    if (params.filter.query) {
        searchParams.set('sort_dir', params.sort.dir);
        searchParams.set('sort_field', params.sort.field);
    }

    if (params.filter.query) {
        searchParams.set('per_page', String(params.pageInfo.pageSize));
        searchParams.set('page_direction', params.pageInfo.pageDirection);
        searchParams.set('cursor', params.pageInfo.cursor);
    }

    const search = searchParams.toString();

    if (isMocksMode()) {
        // return of({
        //     profiles: [...profilesList],
        //     pageInfo: {
        //         hasNextPage: true,
        //         hasPreviousPage: false,
        //     },
        // }).pipe(delay(800));
        return of(profilesList).pipe(delay(800));
    }

    return axios.get<ProfilesResponse>(url + '?' + search.toString()).pipe(map((res) => res.data));
};

const getProfile = (id: string) => {
    const url = `${prefix}/${id}`;

    if (isMocksMode()) {
        return of(profilesList[0]).pipe(delay(800));
    }

    return axios.get<ProfileResponse>(url).pipe(map((res) => res.data));
};

const deleteProfile = (profile: ProfileDTO): Observable<ProfileDTO> => {
    const url = `${prefix}/${profile.id}`;

    return axios.delete<ProfileDTO>(url).pipe(map((res) => res.data));
};

const profilesApi = {
    getProfiles,
    updateProfile,
    applyProfile,
    unassignProfile,
    getProfile,
    deleteProfile,
} as const;

export default profilesApi;
