import axios from './axios';
import {
    Filter,
    FiltersState,
    PageInfo,
    PageInfoResponse,
    PreorderStatus,
    ProductDTO,
    SortState,
    UpdateItemsParams,
} from './types/common';
import productsList from './mocks/productsList';
import productSettings from './mocks/productSettings';
import { Observable, of } from 'rxjs';
import { delay, map } from 'rxjs/operators';
import { ProductSettingsDTO } from './types';
import isMocksMode from './mocks/mock-env';

const prefix = '/products';

export interface ProductsResponse {
    products: ProductDTO[];
    pageInfo: PageInfoResponse;
}

export type ProductSettingsResponse = ProductSettingsDTO;

interface GetProductsParams {
    filter: FiltersState;
    pageInfo: PageInfo;
    sort: SortState;
}

const getProducts = (
    params: GetProductsParams
): Observable<ProductsResponse> => {
    const url = `${prefix}`;
    const searchParams = new URLSearchParams();
    if (params.filter.availiability) {
        searchParams.set('filter', params.filter.availiability);
    }

    if (params.filter.query) {
        searchParams.set('title', params.filter.query);
    }

    searchParams.set('sort_dir', params.sort.dir);
    searchParams.set('sort_field', params.sort.field);
    searchParams.set('per_page', String(params.pageInfo.pageSize));
    searchParams.set('page_direction', params.pageInfo.pageDirection);
    if (params.pageInfo.cursor) {
        searchParams.set('cursor', params.pageInfo.cursor);
    }
    const search = searchParams.toString();

    if (isMocksMode()) {
        return of({
            products: [
                // ...productsList,
                // ...productsList,
                // ...productsList,
                // ...productsList,
                // ...productsList,
                // ...productsList,
                // ...productsList,
                // ...productsList,

                productsList[0],
            ],
            pageInfo: {
                hasNextPage: true,
                hasPreviousPage: false,
            },
        }).pipe(delay(800));
    }
    // eslint-disable-next-line  no-constant-condition
    return axios
        .get<ProductsResponse>(url + '?' + search.toString())
        .pipe(map((res) => res.data));
};

export interface UpdateStatusBulkResponse { }
export interface UpdateSettingsResponse { }

export interface UpdateStatusBulkParams {
    status: PreorderStatus;
    items: {
        productId: string;
        variantId?: string;
    }[];
}

export interface UpdateSettingsParams extends UpdateItemsParams {
    settings: ProductSettingsDTO;
}

const updateProductsAndVariantsStatusBulk = (
    params: UpdateStatusBulkParams
): Observable<UpdateStatusBulkResponse | boolean> => {
    const url = `${prefix}/status`;
    const searchParams = new URLSearchParams();

    if (isMocksMode()) {
        return of(
            true && {
                products: [
                    ...productsList,
                    ...productsList,
                    ...productsList,
                    ...productsList,
                    ...productsList,
                    ...productsList,
                    ...productsList,
                    ...productsList,
                ],
                pageInfo: {
                    hasNextPage: true,
                    hasPreviousPage: false,
                },
            }
        ).pipe(delay(800));
    }

    // eslint-disable-next-line  no-constant-condition
    return axios
        .post<ProductsResponse>(url, params)
        .pipe(map((res) => res.status !== 205));
};

interface GetProductSettingsParams {
    variantId?: string;
    productId: string;
}

const getProductSettings = (
    params: GetProductSettingsParams
): Observable<ProductSettingsResponse> => {
    const url = `${prefix}/settings/${params.productId}`;
    const searchParams = new URLSearchParams();
    if (params.variantId) {
        searchParams.set('variant_id', params.variantId);
    }
    const search = searchParams.toString();

    if (isMocksMode()) {
        return of(productSettings).pipe(
            delay(800)
            // map((_) => {
            //     throw new Error();
            // })
        );
    }
    // eslint-disable-next-line  no-constant-condition
    return axios
        .get<ProductSettingsResponse>(
            url + (params.variantId ? '?' + search.toString() : '')
        )
        .pipe(map((res) => res.data));
};

const updateProductsSettings = (
    params: UpdateSettingsParams
): Observable<UpdateSettingsResponse> => {
    const url = `${prefix}/settings`;
    const searchParams = new URLSearchParams();

    if (isMocksMode()) {
        return of({
            products: [
                ...productsList,
                ...productsList,
                ...productsList,
                ...productsList,
                ...productsList,
                ...productsList,
                ...productsList,
                ...productsList,
            ],
            pageInfo: {
                hasNextPage: true,
                hasPreviousPage: false,
            },
        }).pipe(delay(800));
    }

    // eslint-disable-next-line  no-constant-condition
    return axios
        .post<ProductsResponse>(url, params)
        .pipe(map((res) => res.data));
};

const productsApi = {
    getProducts,
    getProductSettings,
    updateProductsAndVariantsStatusBulk,
    updateProductsSettings,
} as const;

export default productsApi;
