import { ActionsObservable, ofType, StateObservable } from 'redux-observable';
import { from, timer } from 'rxjs';
import {
    catchError,
    exhaustMap,
    map,
    switchMap,
    takeUntil,
    tap,
} from 'rxjs/operators';
import { getUninstallStatus } from '@redux/reducers/uninstall';
import { RootState } from '@redux/reducers/rootReducer';
import appApi from '@api/app';
import { AppStatus } from '@api/types/app';
import {
    getAppState,
    getAppStateFailure,
    getAppStateSuccess,
    startPollAppState,
    stopPollAppState,
} from '@redux/reducers/app';
import { Action } from '@reduxjs/toolkit';

export const getAppStateEpic = (actions$: ActionsObservable<any>) =>
    actions$.pipe(
        ofType<any>(getAppState),
        exhaustMap(() => {
            return appApi.getAppState().pipe(
                switchMap((response) => {
                    const actions: Action<any>[] = [
                        getAppStateSuccess(response),
                    ];
                    if (
                        [AppStatus.Active, AppStatus.Uninstalled].includes(
                            response.app_status
                        )
                    ) {
                        actions.push(stopPollAppState());
                    }

                    return from(actions);
                }),
                catchError((err) =>
                    from([getAppStateFailure(err), stopPollAppState()])
                )
            );
        })
    );

export const pollAppStateEpic = (actions$: ActionsObservable<any>) =>
    actions$.pipe(
        ofType<any>(startPollAppState),
        switchMap(() =>
            timer(0, 5000).pipe(
                takeUntil(
                    actions$.ofType(stopPollAppState)
                    // .pipe(tap((v) => console.log('xxx', v)))
                )
            )
        ),
        map(() => getAppState())
    );
