import { AppStatus } from '@api/types/app';
import { UninstallStatus } from '@api/types/uninstall';
import { statusSelector } from '@redux/selectors/app';
import { DisplayText } from '@shopify/polaris';
import React, { FunctionComponent, useState } from 'react';
import { useSelector } from 'react-redux';
import Start from './Start/Start';
import Complition from './Steps/Complition/Complition';
import Preparation from './Steps/Preparation/Preparation';
import Progress from './Steps/Progress/Progress';
import './uninstall.scss';

type Props = {};

const Uninstall: FunctionComponent<Props> = () => {
    const status = useSelector(statusSelector);
    const [isPreparation, setPreparation] = useState(false);
    const handleNextfromStart = React.useCallback(
        () => setPreparation(true),
        []
    );

    let step = <Start onNext={handleNextfromStart} />;

    switch (status) {
        case AppStatus.Active:
            if (isPreparation) {
                step = <Preparation />;
            }
            break;
        case AppStatus.PrepareToUninstallInProgress:
            step = <Progress />;
            break;
        case AppStatus.Uninstalled:
            step = <Complition />;
            break;
    }

    return (
        <div className="appUninstall">
            <DisplayText size="large">Uninstall</DisplayText>
            {step}
        </div>
    );
};

export default Uninstall;
