import React from 'react';
import { Button } from '@shopify/polaris';
import './preparation.scss';
import { startUninstallPreparation } from '@redux/reducers/uninstall';
import { useDispatch } from 'react-redux';

function Preparation() {
    const dispatch = useDispatch();
    const handleStart = React.useCallback(() => {
        dispatch(startUninstallPreparation());
    }, []);

    return (
        <div className="appUninstall__preparation">
            <div className="appUninstall__step__content">
                Preparation for application removal
            </div>
            <Button onClick={handleStart}>Start Preparation</Button>
        </div>
    );
}

export default Preparation;
