import { ActionFrom } from '@api/types';
import { intl } from '@contexts/LocaleContext';
import {
    handlePrimaryConfirmationAction,
    handleSecondaryConfirmationAction,
    startAppLoading,
    stopAppLoading,
    toggleConfirmation,
    toggleToast,
} from '@redux/reducers/app';
import { RootState } from '@redux/reducers/rootReducer';
import { Action, ActionCreatorWithPayload } from '@reduxjs/toolkit';
import { MessageDescriptor } from 'react-intl';
import { ActionsObservable, ofType, StateObservable } from 'redux-observable';
import { EMPTY, from, iif, Observable, of, Subject } from 'rxjs';
import { filter, first, map, startWith, switchMap, take } from 'rxjs/operators';
import messages from './messages';

const confirmationControl = (function () {
    // const innerActions$ = new Subject<Action>();
    let innerActions$: ActionsObservable<any>;
    let innerState$: StateObservable<RootState>;

    const innerActionEpic = (
        actions$: ActionsObservable<any>,
        state$: StateObservable<RootState>
    ) => {
        // actions$.subscribe(innerActions$);
        innerActions$ = actions$;
        innerState$ = state$;
        return EMPTY;
    };

    const withConfirmation = <T extends ActionCreatorWithPayload<any, string>>(
        confirmationState: (
            action: ActionFrom<T>
        ) => ActionFrom<typeof toggleConfirmation>['payload'],
        afterConfirmation?: (
            action: ActionFrom<T>
        ) => Observable<{
            payload: any;
            type: string;
        }>
    ) => (source: Observable<any>) => {
        return source.pipe(
            switchMap((action: ActionFrom<T>) =>
                innerActions$
                    .pipe(
                        ofType(
                            handlePrimaryConfirmationAction,
                            handleSecondaryConfirmationAction
                        ),
                        take(1),
                        switchMap((innerAction: { type: string }) => {
                            if (
                                !afterConfirmation ||
                                innerAction.type ===
                                handleSecondaryConfirmationAction.toString()
                            ) {
                                return of({ type: 'noop' });
                            }

                            return afterConfirmation(action);
                        })
                    )
                    .pipe(
                        startWith(toggleConfirmation(confirmationState(action)))
                    )
            )
        );
    };

    const withAppLoading = <T extends ActionCreatorWithPayload<any, string>>(
        handler: (
            action: ActionFrom<T>,
            state: RootState
        ) => Observable<{
            payload: any;
            type: string;
        }>
    ) => (source: Observable<any>) => {
        return source.pipe(
            switchMap((action: ActionFrom<T>) =>
                handler(action, innerState$.value).pipe(
                    switchMap((action: ActionFrom<T>) =>
                        from([action, stopAppLoading()])
                    ),
                    startWith(startAppLoading())
                )
            )
        );
    };

    const mapWithToast = <T extends ActionCreatorWithPayload<any, string>>(
        toast:
            | MessageDescriptor
            | (Omit<
                NonNullable<Parameters<typeof toggleToast>[0]>,
                'content'
            > & {
                content: MessageDescriptor;
            }),
        mapResult: (...args: any) => ActionFrom<any>
        // handler: (
        //     action: ActionFrom<T>,
        //     state: RootState
        // ) => Observable<{
        //     payload: any;
        //     type: string;
        // }>
    ) => (source: Observable<any>) => {
        let content: string;
        let payload;
        if ('content' in toast) {
            content = intl.formatMessage(toast.content);
            payload = { ...toast, content };
        } else {
            content = intl.formatMessage(toast);
            payload = { content };
        }

        return source.pipe(
            switchMap((result: any) =>
                // handler(action, innerState$.value).pipe(
                //     switchMap((action: ActionFrom<T>) => of(action)),
                //     startWith(toggleToast(payload))
                // )
                from([mapResult(result), toggleToast(payload)])
            )
        );
    };

    return { innerActionEpic, withConfirmation, withAppLoading, mapWithToast };
})();

export const toggleBulkEnableWarning = () =>
    toggleToast({
        content: intl.formatMessage(messages.bulkEnableWarning),
        duration: 30000,
    });

export const {
    innerActionEpic,
    withConfirmation,
    withAppLoading,
    mapWithToast,
} = confirmationControl;
